import { Link } from "react-router-dom";
import "./searchItem.css";
import React from "react";
import { SearchContext } from "../../context/SearchContext";
import { useState, useContext } from "react";
import useFetch from "../../hooks/useFetch";
import { getDatesInRange } from "../../function"
// nhung HOTEL hien tren thanh search
const SearchItem = ({ item }) => {
  const { dates, options } = useContext(SearchContext);
  const [roomInSearchItem, setroomInSearchItem] = useState(1);
  // ly ra cac loai phong cua hotel nay
  const { data: allTypeRoom, loading, error, reFetch } = useFetch(`/rooms/${item._id}/?status=true`);
  // console.log(allTypeRoom)
  const MILLISECONDS_PER_DAY = 1000 * 60 * 60 * 24;
  function dayDifference(date1, date2) {
    const timeDiff = Math.abs(date2.getTime() - date1.getTime());
    const diffDays = Math.ceil(timeDiff / MILLISECONDS_PER_DAY);
    return diffDays;
  }

  // tinh toan  hin thi gia va option theo la chon ngi dung - theo phong re nht cua hotel y
  const days = dayDifference(dates[0].endDate, dates[0].startDate);
  const calculatePrice = (cheapestPrice) => {
    let totalPrice = 0;
    let totalPeople = parseInt(options.adult, 10) + parseFloat(options.children) * 0.5; // tre em tinh 0.5
    // nu ngi dung co la chon s phong , totalPeople / cheapestPrice.people  tinh s phong co th cha c totalPeople.
    // Math floor lam tron xung s nguyn gn nht 1.2 =>1
    if (options.room > Math.floor(totalPeople / cheapestPrice.people)) {
      totalPrice = cheapestPrice.price * options.room * days;
    } else
      // nu s ngi ngi dung chon it hn so vi sc cha cua phong re nht (VD: chon 1 ngi ma phong re nht hotel o cha 2 ngi)
      if (Math.floor(totalPeople / cheapestPrice.people) == 0) {
        totalPrice = cheapestPrice.price * days;
      } else {
        totalPrice = cheapestPrice.price * Math.floor(totalPeople / cheapestPrice.people) * days;
      }
    // nhn 1000 chi la  hin thi, con loc gia  trang list ko nhn 1000
    return Intl.NumberFormat('vi-VN').format(totalPrice * 1000)

  };

  const calculateRoom = (cheapestPrice) => {
    let totalPeople = parseInt(options.adult, 10) + parseFloat(options.children) * 0.5;
    // nu s phong ngi dung chon nhiu hn phong co th cha totalPeople thi tinh theo options.room o
    if (options.room > Math.floor(totalPeople / cheapestPrice.people)) return options.room
    // tinh s phong theo Math.floor(totalPeople / cheapestPrice.people)
    else {
      // VD:11/5=2
      return Math.floor(totalPeople / cheapestPrice.people)
    }
  };

  const alldates = getDatesInRange(dates[0].startDate, dates[0].endDate);
  const isAvailable = (roomNumber, dateToCheck) => {
    // if (!roomNumber.status) {
    //   return false; // Nu status l false, room khng kh dng
    // }
    const isFound = roomNumber.unavailableDates.some((date) => {
      const dateMinusOneDay = new Date(date).getTime(); // theem getTIme() hay ko cung v
      // console.log(new Date(dateMinusOneDay));
      return dateToCheck == dateMinusOneDay;
    });

    return !isFound;
  };
  // console.log(alldates)
  // tinh tng phong available cua mi loai phong
  const calculateAvailableRoomsEveryTypeRoom = (roomNumbers) => {
    let roomAvailable = 999;
    for (let date of alldates) {
      let dateAvailableCount = 0;
      //Vi mi date, duyt qua cac phn t trong mng roomNumbers
      for (let roomNumber of roomNumbers) {
        // Kim tra xem phng o co date hin tai trng ko
        if (isAvailable(roomNumber, date)) {
          // co phong thoa man date hin tai
          dateAvailableCount++
        }
      };
      // vi mi date sau khi lp ht cac room nho, cp nht roomAvailable (roomAvailable se la 
      // dateAvailableCount nho nht trong tt ca cac date )
      if (dateAvailableCount < roomAvailable) {
        roomAvailable = dateAvailableCount
      }
     
    }
    return roomAvailable
  }
   // tinh tng phong avalaible cua hotel o
  const caculateTotalRoomsAvailable = () => {
    let totalRooms = 0;
    totalRooms = allTypeRoom.reduce((total, typeRoom) => {
      // Vi mi loai phong tinh toan s phong available cua loai phong o, truyn vao roomNumbers
      const availableRooms = calculateAvailableRoomsEveryTypeRoom(typeRoom.roomNumbers)
      // Thm s lng phng t typeRoom vo tng s lng phng
      return total + availableRooms;
    }, 0);
    return totalRooms
  }
  // tinh tng phong avalaible cua hotel o
  const totalRooms = caculateTotalRoomsAvailable()
  // nu ht phong thi ko hin hotel o
  if (totalRooms === 0) {
    return null;
  }
  return (
    <div className="searchItem">
      <img src={item.photos[0]} alt="" className="siImg" />

      <div className="siDesc">
        <h1 className="siTitle">{item.name}</h1>
        <span className="siDistance">Khoang cach: {item.distance}m t trung tm</span>
        { totalRooms == 0 ? (
          <span className="siRoomLeft">Ht phng!</span>
        ) : (
          totalRooms && (totalRooms < 6 || totalRooms < calculateRoom(item.cheapestPrice)) && (
            <span className="siRoomLeft">Ch cn {totalRooms} phng!</span>
          )
        )}
        {/* <span className="siFeatures">{item.desc}</span> */}
        {/* <span className="siCancelOp">Free cancellation </span>
        <span className="siCancelOpSubtitle">
          You can cancel later, so lock in this great price today!
        </span> */}
        <span className="siFeatures">ia chi: {item.address}</span>
        <span className="siFacilities">
          {item.facilities?.map((facility, index) => (
            <div className="siFacilities_item" style={{ width: '30%' }} key={index}>
              {facility}
            </div>
          ))}
        </span>
      </div>

      <div className="siDetails">
        {/* {item.rating && <div className="siRating">
          <span>Excellent</span>
          <button>{item.rating}</button>
        </div>} */}

        <div className="siDetailTexts">
          <span className="siPrice">Gia t: {calculatePrice(item.cheapestPrice)} VND</span>
          <span className="siTaxOp">Cho {options.adult} ngi, {options.children > 0 && `, ${options.children} tr em`}, {calculateRoom(item.cheapestPrice)} phong, {days} m</span>
          {/* chuyen sang xem thong tin tung hotel */}

          <Link to={`/hotels/${item._id}`}>
            <button className="siCheckButton">Xem ch trng</button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default SearchItem;
