import React from "react";
import axios from "axios";
import { useContext, useState } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../../context/AuthContext";

const Register = () => {
  const [credentials, setCredentials] = useState({
    username: undefined,
    password: undefined,
    email:undefined,
    passwordRepeat: undefined // Thm trng passwordRepeat vo state, trng nay khi y vao API ko dung

  });

  const {user, loading, error, dispatch } = useContext(AuthContext);

  const navigate = useNavigate()
  // khi thay doi field
  const handleChange = (e) => {
    setCredentials((prev) => ({ ...prev, [e.target.id]: e.target.value }));
  };

  // submit
  const handleClickRegister = async (e) => {
    e.preventDefault();
    if (credentials.password !== credentials.passwordRepeat) {
      alert("Mt khu khng khp");
      return;
    }
    try {

      const res = await axios.post("/auth/register", credentials);
      alert("ng ky thanh cng")
      navigate("/login")
    } catch (err) {
      dispatch({ type: "LOGIN_FAILURE", payload: err.response.data });
    }
  };

  return (
    <div className="login">
      <div className="lContainer">
      <h1 className="Login">ng ky</h1>
        <input
          type="text"
          placeholder="Tn ng nhp"
          id="username"
          onChange={handleChange}
          className="lInput"
        />
        <input
          type="password"
          placeholder="Mt khu"
          id="password"
          onChange={handleChange}
          className="lInput"
        />

          <input
          type="password"
          placeholder="Nhp lai mt khu"
          id="passwordRepeat"
          onChange={handleChange}
          className="lInput"
        />  

          <input
          type="email"
          placeholder="Email"
          id="email"
          onChange={handleChange}
          className="lInput"
        />

        
       
          {/* <label>
            <input 
            type="checkbox" 
            name="remember" 
            /> 
            Remember Me
          </label> */}
         
      
        
        <button disabled={loading} onClick={handleClickRegister}   className="lButton btn_registerr">
          ng ky
        </button>
        {error && <span>Tn ng nhp hoc email <br></br> a co ngi s dung</span>}
      </div>
    </div>
  );
};

export default Register;