import "./list.css";
import React from "react";
import Navbar from "../../../components/navbar/Navbar";
import Header from "../../../components/header/Header";
import { useLocation } from "react-router-dom";
import { useState, useContext, useEffect } from "react";
import { format } from "date-fns";
import { DateRange } from "react-date-range";
import SearchItem from "../../../components/searchItem/SearchItem";
import useFetch from "../../../hooks/useFetch";
import { SearchContext } from "../../../context/SearchContext";
import { addDays, addHours } from 'date-fns';
import { listProvinces } from "../../../listObject";
import { hotelFacilities } from "../../../formSource";
import Footer from "../../../components/footer/Footer";
const List = () => {
  const timeZone = new Date().getTimezoneOffset() / 60  //  lch mui gi so vi UTC cua may hin tai
  const location = useLocation();
  const searchContext = useContext(SearchContext);
  const [destination, setDestination] = useState(searchContext.destination);
  const [dates, setDates] = useState(searchContext.dates);
  const [openDate, setOpenDate] = useState(false);
  const [options, setOptions] = useState(searchContext.options);
  const [suggestedDestination, setSuggestedDestination] = useState([]);
  const [min, setMin] = useState(100);
  const [max, setMax] = useState(100000);
  const [type, setType] = useState(location.state?.typeFromPropertyList ?? "");
  const [selectedFacilities, setSelectedFacilities] = useState([]);
  const [sortOrder, setSortOrder] = useState('random');
  // console.log(dates)
  const { data, loading, error, reFetch } = useFetch(
    `/hotels?city=${destination}&type=${type}`
  );
  const { dispatch } = useContext(SearchContext);
  // checkbox facilities
  const handleCheckboxChange = (facility) => {
    setSelectedFacilities((prevSelected) => {
      // nu ngi dung tich cai a co - tc bo no i thi bo no khoi mang
      if (prevSelected.includes(facility)) {
        return prevSelected.filter((item) => item !== facility);
      } else {
        return [...prevSelected, facility];
      }
    });
  };
  // console.log(selectedFacilities)
  // gi y tim kim
  const handleDestinationChange = (e) => {
    setDestination(e.target.value);
    if (e.target.value) {
      const filteredSuggestions = listProvinces.filter(province =>
        province.name.toLowerCase().startsWith(e.target.value.toLowerCase())
      ).map(province => province.name);
      setSuggestedDestination(filteredSuggestions);
    } else {
      setSuggestedDestination([]);
    }
  };
  const handleSuggestionClick = (suggestion) => {
    setDestination(suggestion);
    setSuggestedDestination([]);
  };

  // loai ch nghi
  const handleChangeType = (event) => {
    setType(event.target.value);
  };
  const handleClick = () => {
    reFetch();
    dispatch({ type: "NEW_SEARCH", payload: { destination, dates, options } });
  };

  useEffect(() => {
    // console.log("Updated changes:", dates);
    dispatch({ type: "NEW_SEARCH", payload: { destination, dates, options } });
  }, [destination, dates, options]);


  const handleDayChange = (item) => {
    const utc = new Date().getTimezoneOffset() / 60 //-7
    const newSelection = { ...item.selection };
    let { startDate, endDate } = newSelection;
    if (startDate === endDate) {
      // nu ngi dung chi chon 1 ngay
      endDate = addDays(new Date(startDate), 1);
    }
    // 14+ getTimezoneOffset Mui gi lch  khach san ma no t -dung addHours
    startDate = addHours(startDate, 7 - utc);
    endDate = addHours(endDate, 7 - utc);
    setDates([{ ...newSelection, startDate, endDate }]);
  };

  const handleOptionChange = (e, optionName) => {
    const value = e.target.value;
    setOptions(prevOptions => ({
      ...prevOptions,
      [optionName]: value
    }));
  };

  const MILLISECONDS_PER_DAY = 1000 * 60 * 60 * 24;
  function dayDifference(date1, date2) {
    const timeDiff = Math.abs(date2.getTime() - date1.getTime());
    const diffDays = Math.ceil(timeDiff / MILLISECONDS_PER_DAY);
    return diffDays;
  }
  const days = dayDifference(dates[0].endDate, dates[0].startDate);

  // tinh gia hin thi theo phong re nht cua ks va la chon cua ngi dung, ko hin nhng phong cha co phong- phep chia cho 0-cheapestPrice.people
  const calculatePrice = (cheapestPrice) => {
    let totalPrice = 0;
    let totalPeople = parseInt(options.adult, 10) + parseFloat(options.children) * 0.5;

    // nu s lng phong ng dung chon a >= 
    if (options.room > Math.floor(totalPeople / cheapestPrice.people)) {
      totalPrice = cheapestPrice.price * options.room * days;
    } else
      // console.log(totalPeople)
      // nu s ngi =1 hoc qua it 1/2=0
      if (Math.floor(totalPeople / cheapestPrice.people) == 0) {
        totalPrice = cheapestPrice.price * days;
      } else {
        //new Intl.NumberFormat('vi-VN').format(params.value*1000)
        totalPrice = cheapestPrice.price * Math.floor(totalPeople / cheapestPrice.people) * days;
      }
    return totalPrice
  };

  // sort gia
  const handleSortChange = (event) => {
    setSortOrder(event.target.value); // Cp nht state khi ngi dng thay i cch sp xp
  };

  const renderSearchItem = () => {
    const filteredData = data
      // sp xp theo gi
      .sort((a, b) => {
        const priceA = calculatePrice(a.cheapestPrice);
        const priceB = calculatePrice(b.cheapestPrice);
        if (sortOrder === 'asc') {
          return priceA - priceB;
        } else if (sortOrder === 'desc') {
          return priceB - priceA;
        } else if (sortOrder === 'random') {
          // nu ngi dung ko sort theo gia thi se sort theo tng s lng phong (nu options.room>4 con ko thi random)
          if (options.room > 4) {
            return b.totalRooms - a.totalRooms;
          }
          return Math.random() - 0.5;
        }
      })
      // ly ra cc phn t trong khong gi - calculatePrice l tnh gi theo option ngi dng nh ngy, s phng...
      // ly theo gi phng r nht ca ch ngh 
      .filter((item) => calculatePrice(item.cheapestPrice) >= min && calculatePrice(item.cheapestPrice) <= max)
      // lc theo c s vt cht
      .filter(hotel => {
        // tr v true nu tt c phn t tha mn - ly ra cc phn t c facility ngi dng tch
        return selectedFacilities.every(facility => hotel.facilities?.includes(facility));
      });

    if (filteredData.length === 0) {
      return <p>Khng c ch ngh tha mn yu cu ca bn.</p>;
    }
    return filteredData.map((item) => (
      <SearchItem item={item} key={item._id} />
    ));
  }



  return (
    <div>
      <Navbar />
      <Header type="list" />
      <div className="listContainer">
        <div className="listWrapper">
          <div className="listSearch">
            <h1 className="lsTitle">Tim kim</h1>
            <div className="lsItem">
              <label>ia im</label>
              <input placeholder={destination} value={destination} onChange={handleDestinationChange} type="text" />
              {suggestedDestination.length > 0 && (
                // style inline  e 1 s cai khac so vi header, css con lai  header.css
                <div className="suggestionDestination" style={{ width: '94%' }}>
                  <div className="suggestionsList">
                    {suggestedDestination.map((suggestion, index) => (
                      <div
                        key={index}
                        style={{  }}
                        onClick={() => handleSuggestionClick(suggestion)}
                      >
                        {suggestion}
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
            <div className="lsItem">
              <label>Check-in</label>
              <span onClick={() => setOpenDate(!openDate)}>{`${format(
                dates[0].startDate,
                "dd/MM/yyyy"
              )} n ${format(dates[0].endDate, "dd/MM/yyyy")}`}</span>
              {openDate && (
                <DateRange
                  onChange={(item) => handleDayChange(item)}
                  moveRangeOnFirstSelection={true}
                  minDate={addHours(new Date(),10+(-7-timeZone))} // ngay ti thiu c chon, 
                  maxDate={addDays(new Date(),365)}
                  ranges={dates}
                />
              )}
            </div>

            {/* CHon loai cho nghi */}
            <div className="lsItem">
              <label>Chon loai ch nghi</label>
              <select style={{ height: '25px' }}
                id='type'
                onChange={handleChangeType}
                value={type}
              >
                <option value="">Tt ca</option> {/* Option mc nh */}
                <option value="Khach san">Khach san</option> {/* Cc option ca dropdown */}
                <option value="Cn h">Cn h</option>
                <option value="Bit th">Bit th</option> {/* Cc option ca dropdown */}
                <option value="Resort">Resort</option>
                <option value="Nha nghi">Nha nghi</option>
              </select>
            </div>


            <div className="lsItem">
              {/*  chinh gia tien */}
              <label>Khoang gia (theo gia loai phong re nht cua ch nghi) </label>
              <div className="lsOptions">
                <div className="lsOptionItem">
                  <span className="lsOptionText">
                    T <small></small>
                  </span>

                  <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: '10px' }}>
                    <div>({Intl.NumberFormat('vi-VN').format(min * 1000)} VND)</div>
                    <input
                      type="number"
                      min="0"
                      value={min}
                      onChange={(e) => {
                        const value = Math.max(0, e.target.value);
                        setMin(value);
                      }}
                      className="lsOptionInput"
                    />
                  </div>
                </div>

                <div className="lsOptionItem">
                  <span className="lsOptionText">
                    n <small></small>
                  </span>
                  <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: '10px' }}>
                    <div>({Intl.NumberFormat('vi-VN').format(max * 1000)} VND)</div>
                    <input
                      type="number"
                      value={max}
                      min="0"
                      onChange={(e) => setMax(e.target.value)}
                      className="lsOptionInput"
                    />
                  </div>
                </div>

              </div>
            </div>
            {/* ang css tam */}
            <div style={{ marginTop: '-15px' }} className="lsItem">
              {/*  chinh gia tien */}
              <label>La chon </label>
              <div className="lsOptions">
                <div className="lsOptionItem">

                  <span className="lsOptionText">Ngi ln</span>
                  <input
                    type="number"
                    min={1}
                    className="lsOptionInput"
                    placeholder={options.adult}
                    onChange={(e) => handleOptionChange(e, 'adult')}
                  />
                </div>
                <div className="lsOptionItem">
                  <span className="lsOptionText">Tre em</span>
                  <input
                    type="number"
                    min={0}
                    className="lsOptionInput"
                    placeholder={options.children}
                    onChange={(e) => handleOptionChange(e, 'children')}

                  />
                </div>
                <div className="lsOptionItem">
                  <span className="lsOptionText">Phong</span>
                  <input
                    type="number"
                    min={1}
                    className="lsOptionInput"
                    placeholder={options.room}
                    onChange={(e) => handleOptionChange(e, 'room')}

                  />
                </div>
              </div>
            </div>

            <div className="lsItem">
              <label>C s vt cht ch nghi</label>
              {hotelFacilities.map((facility) => (
                <div className="lsItem_hotelFacilities" key={facility}>
                  <label>
                    <input
                      type="checkbox"
                      value={facility}
                      onChange={() => handleCheckboxChange(facility)}
                      checked={selectedFacilities?.includes(facility)}
                    />
                    {facility}
                  </label>
                </div>
              ))}
            </div>
            <button onClick={handleClick}>Tim kim</button>
          </div>
          <div className="listResult">
            {/* dropdown sort theo gia */}
            <div style={{ textAlign: 'right', marginBottom: '10px' }}>
              <label>
                Sp xp theo gi:&nbsp;
                <select value={sortOrder} onChange={handleSortChange}>
                <option value="random">Ngu nhin</option>
                  <option value="asc">Thp n cao</option>
                  <option value="desc">Cao n thp</option>
                </select>
              </label>
            </div>

            {loading ? (
              "loading"
            ) : (
              <>

                {renderSearchItem()}
              </>
            )}
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};
export default List;