import { createContext, useEffect, useReducer } from "react";
import React from "react";
// khi dispatch co user,loading,error, user tra ve action.payload la thong tin tk trong csdl
const INITIAL_STATE = {
  user:  JSON.parse(localStorage.getItem("user")) ||null,
  loading: false,
  error: null,
};
// khi ma user = AuthContext() thi mc inh user se co nhng th trn
export const AuthContext = createContext(INITIAL_STATE);

// quan ly cac trang thai cua context- cac action nh LOGIN_START  cp nht trang thai context
const AuthReducer = (state, action) => {
  switch (action.type) {
    //   luc bam nut dang nhap
    case "LOGIN_START": 
      return {
        user: null,
        loading: true,
        error: null,
      };
    case "LOGIN_SUCCESS":
      return {
        user: action.payload,
        loading: false,
        error: null,
      };
      // khi co action gui sang  dispatch({ type: "LOGIN_SUCCESS", payload: res.data });
      // se co thuc tinh payload, action.payload  truy cp vao o
    case "LOGIN_FAILURE":
      return {
        user: null,
        loading: false,
        error: action.payload,
      };
    case "LOGOUT":
      return {
        user: null,
        loading: false,
        error: null,
      };
    default:
      return state;
  }
};

export const AuthContextProvider = ({ children }) => {
  const [state, dispatch] = useReducer(AuthReducer, INITIAL_STATE);

  useEffect(() => {
      // luu giu user khi refresh, luu state thanh 1 chuoi vao local storage, khi bin user thay i thi ham nay goi, thnng
      // tin user se lu trong localstorage
    // const userWithoutAdmin = { ...state.user };
    // delete userWithoutAdmin.isAdmin;
    const userInfoLocalStorage = { username: state.user?.username };
    localStorage.setItem("user", JSON.stringify(userInfoLocalStorage));
  }, [state.user]);

  return (
    <AuthContext.Provider
      value={{
        user: state.user,
        loading: state.loading,
        error: state.error,
        dispatch,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};