import React from 'react'
import './listReservation.css'
import { DataGrid } from "@mui/x-data-grid";
import { ReservationColumns } from '../../../datatablesource';
import useFetch from '../../../hooks/useFetch';
import { useContext, useState, useEffect } from 'react'
import { AuthContext } from '../../../context/AuthContext';
import { Link, useLocation } from "react-router-dom";
import Sidebar from '../../../components/adminComponents/sidebar/Sidebar'
import NavbarAdmin from '../../../components/adminComponents/navbarAdmin/NavbarAdmin'
import { DateRange } from "react-date-range";
import { format, addDays, addYears, subYears } from "date-fns";
import { SearchContext } from '../../../context/SearchContext'
import { toast } from 'react-toastify';
import {
    faBed,
    faCalendarDays,
    faPerson,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { jwtDecode } from "jwt-decode";
import Modal from 'react-modal';
import axios from 'axios';
const currentDate = new Date();
//  hin tt ca reservations (khi ngi dung cha loc theo ngay)
const INITIAL_STATE =
    [
        {
            startDate: subYears(currentDate, 100), // ngay t 100 nm trc va sau 100 nm
            endDate: addYears(currentDate, 100),
            key: "selection",
        },
    ];

const ListReservation = () => {
    const { user } = useContext(AuthContext) // {user._id}
    // const token = document.cookie.replace(/(?:(?:^|.*;\s*)access_token\s*=\s*([^;]*).*$)|^.*$/, "$1");
    // const decodedToken = jwtDecode(token);
    const searchContext = useContext(SearchContext);
    const [dates, setDates] = useState(searchContext.dates);
    //date nay  loc trong API query
    const [datesToFilter, setDatesToFilter] = useState(INITIAL_STATE);
    const { data: reservationData, loading: reservationLoading, error: reservationError,
        reFetch: reservationReFetch } = useFetch(`/reservation/admin?startDay=${datesToFilter[0].startDate}&endDay=${datesToFilter[0].endDate}`);
    const [openDate, setOpenDate] = useState(false);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [isRequesting, setIsRequesting] = useState(false);
    const [reasonCancel, setReasonCancel] = useState('');
    const [selectedCancelReservation, setSelectedCancelReservation] = useState("");
    // console.log(selectedCancelReservation)
    // i gia tri hin thi trn lich - ko cn phai chun gi vi bn backend se quet t 0h ngay o theo UTC
    const handleDayChange = (item) => {
        const newSelection = { ...item.selection };
        const { startDate, endDate } = newSelection;
        startDate.setHours(14, 0, 0, 0);
        endDate.setHours(14, 0, 0, 0);
        // dates nay  hin thi lich trn giao din
        setDates([{ ...newSelection, startDate, endDate }]);
    };

    // khi bm loc mi lu vao bin trong API query
    const filterByDates = () => {
        //date nay  loc trong API query
        setDatesToFilter(dates);
    }

    const cancelFilterByDates = () => {
        //date nay  loc trong API query - huy loc theo ngay ly tt ca n
        setDatesToFilter(INITIAL_STATE);
    }

    // m modal yu cu huy
    const closeModalCancelRequest = () => {
        setModalIsOpen(false)
    }
    const openRequestCancelModal = (canceledReservation) => {
        // kim tra nu thi gian nhn phong n y a qua hoc n y  status khac 1 (tc khac thanh cng) la huy thi ko c 
        if (new Date() > new Date(canceledReservation.start) || canceledReservation.status !== 1) {
            toast.error("Ban khng th yu cu huy n nay!")
            return;
        }
        setSelectedCancelReservation(canceledReservation)
        setModalIsOpen(true)
    }


    // khi admin bm yu cu huy n t
    const requestCancel = async () => {
        setIsRequesting(true)
        let hasError = false
        try {
            // chinh lai reservation nay la do admin yu cu huy
            try {
                await axios.put(`/reservation/${selectedCancelReservation._id}`, {
                    cancelDetails: {
                        isAdminCancel: true
                    }
                })
            } catch (err) {
                hasError = true;
                console.log(err)
                return;
            }

            // gi email xac nhn cho khach v yu cu huy cua admin
            // try {
            //     const res = await axios.put(`/reservation/email/sendEmailStatusReservation`, {
            //         userId: selectedCancelReservation.userId, // gi cho email cua account khach - userId khach
            //         emailSubject: "THNG BAO YU CU HUY N T PHONG T CHU CH NGHI ",
            //         emailContent: `n t phong ma ${selectedCancelReservation._id} cua quy khach c chu ch nghi yu cu huy \n Ly do huy: ${reasonCancel} \n Chi tit xem tai trang web phn n t phong `
            //     });
            // } catch (err) {
            //     console.log(err)
            //     hasError = true;
            //     return;
            // }

        } catch (err) {
            console.log(err)
        } finally {
            if (hasError) {
                toast.error(" xy ra li. Vui long th lai.");
                setModalIsOpen(false)
            } else {
                toast.success("Gi yu cu huy thnh cng");
                setModalIsOpen(false)
            }
            // setIsSending(false)
            setIsRequesting(false)
            reservationReFetch();
        }

    }
    // thm ct xoa sa
    const actionColumn = [
        {
            field: "action",
            headerName: "Yu cu huy",
            flex:1,
            minWidth: 120,
            headerAlign: 'center',

            renderCell: (params) => {
                const { cancelDetails, status } = params.row;
                const isAdminCancel = cancelDetails?.isAdminCancel;
                // khi n co y/c huy t admin
                if (isAdminCancel) {
                 // khi n co yu cu huy t admin nhng cha c khach chp nhn - status n =1 la thanh cng
                    if(status ==1){
                        return (
                            <div className="cellRequestCancel">
                                <div style={{color:"blueviolet"}}>a yu cu huy</div>
                            </div>
                        );
                    } else{
                    // khi n co yu cu huy t admin va c khach chp nhn
                        return (
                            <div className="cellRequestCancel">
                                <div style={{color:"blueviolet", whiteSpace:'normal',textAlign:'center'}}>Yu cu huy thanh cng</div>
                            </div>
                        );
                    }
                   
                }
                // trng hp n  trang thai huy hoc qua ngay thi ko hin yu cu huy
                if (new Date() > new Date(params.row.start) || params.row.status !== 1) {

                }else{
                    return (
                        <div className="cellRequestCancel">
                            <div className="requestCancelButton" onClick={() => openRequestCancelModal(params.row)}>
                                Yu cu huy
                            </div>
                        </div>
                    );
                }
              
            },
        },
    ];
    return (
        <div className="listAdmin">
            <Sidebar />
            {/* css t adminHome css */}
            <div className="listContainerAdmin">
                <NavbarAdmin />

                {/* Modal */}
                <div className="ListReservationAdminContainer">
                    { modalIsOpen && (
                        <div className="modalCancelRequest">
                            <div className="modal-content">
                                <span className="modalCancelRequest_close" onClick={closeModalCancelRequest}>&times;</span>
                                <div className="modalCancelRequest_title">YU CU HUY PHONG</div>
                                <textarea  type="text" value={reasonCancel} rows="4"
                                 placeholder="Nhp ly do mun huy. Yu cu huy va ly do se c gi n email cua khach. Lu y rng n t phong vn se co hiu lc cho n khi khach ng y huy."
                                 onChange={(e) => setReasonCancel(e.target.value)} />
                                <div >
                                    <button className="modalCancelRequest_btn" disabled={isRequesting}  onClick={requestCancel}> {isRequesting ? 'ang x l...' : 'Yu cu hy'}</button>
                                </div>

                            </div>
                        </div>
                    )}
                    <h2>Nhng n t phong tai ch nghi cua ban</h2>

                    <div style={{ display: 'flex', justifyContent: 'flex-start', gap: "10px", marginBottom: '10px', textAlign: 'center' }}>
                        {/* css t listRoomCLient */}
                        <div style={{ width: '20%' }} className="ListReservationAdminContainer_searchDates">
                            <FontAwesomeIcon icon={faCalendarDays} className="ListReservationAdminContainer_icon" />
                            <span onClick={() => setOpenDate(!openDate)}>{`${format(
                                dates[0].startDate,
                                "dd/MM/yyyy"
                            )} to ${format(dates[0].endDate, "dd/MM/yyyy")}`}</span>
                            {openDate && (
                                <DateRange
                                    onChange={(item) => handleDayChange(item)}
                                    // minDate={new Date()}
                                    ranges={dates}
                                    moveRangeOnFirstSelection={false}
                                    editableDateInputs={true}
                                    className="date"
                                />
                            )}
                        </div>

                        <button className="ListReservationAdminContainer_filterBtn" onClick={filterByDates}>Loc theo ngay</button>
                        <button className="ListReservationAdminContainer_filterBtn" onClick={cancelFilterByDates}>Huy loc</button>
                        <div style={{ fontStyle: 'italic' }}>(Loc theo ngay c tinh theo ngay check in cua n, vi du nu <br /> khoang ngay 19 n 20 se ly n co check in ngay 19 va 20)</div>
                    </div>




                    <DataGrid autoHeight getRowHeight={() => 'auto'}
                        // className="datagrid"
                        sx={{
                            '&.MuiDataGrid-root--densityCompact .MuiDataGrid-cell': { py: '8px' },
                            '&.MuiDataGrid-root--densityStandard .MuiDataGrid-cell': { py: '15px' },
                            '&.MuiDataGrid-root--densityComfortable .MuiDataGrid-cell': { py: '22px' },
                          }}
                        rows={reservationData}
                        columns={ReservationColumns.concat(actionColumn)}
                        pageSize={8}
                        rowsPerPageOptions={[5]}
                        // checkboxSelection
                        getRowId={(row) => row._id}
                    />
                </div>


            </div>
        </div>

    )
}

export default ListReservation
