import React from 'react'
import "./listBooking.css"
import Navbar from '../../../components/navbar/Navbar'
import Header from '../../../components/header/Header'
import { useState, useContext } from "react";
import axios from 'axios';
import useFetch from '../../../hooks/useFetch';
import { AuthContext } from '../../../context/AuthContext';
import { format, addDays, subDays, subHours, addHours } from "date-fns";
import { confirmAlert } from 'react-confirm-alert';
import { toast } from 'react-toastify';
import { Link } from 'react-router-dom';
import Footer from '../../../components/footer/Footer';
const ListBooking = () => {
  // const { user } = useContext(AuthContext)
  const { data, loading, error, reFetch } = useFetch(
    `/reservation/client`
  );
  const [isSending,setIsSending] = useState(false)

  const handleCancelReserve = async (selectedReservation) => {
    let message = ""
    let cancelFee =0;
    // huy trong khoang time 3 ngay trc ngay nhn phong va ko trong khoang 24h sau thi gian t 
    // va ko phai la yu cu huy t admin thi bi coi la mun - tinh phi m u
    const isLateCancel = (new Date() > subHours(new Date(selectedReservation.start), 24 * 3)) &&  !selectedReservation.cancelDetails.isAdminCancel
      && (new Date() > addHours(new Date(selectedReservation.createdAt), 24))

    if (isLateCancel) {
      cancelFee = selectedReservation.totalPrice / selectedReservation.allDatesReserve.length
      message = `Ban co chc chn mun huy n t phong nay? Ban khng th hoan tac sau khi huy. Ban mt phi huy 
      (phi m u- ${new Intl.NumberFormat('vi-VN').format(cancelFee* 1000)} VND)
       nu huy n t nay.`
    } else message = "Ban co chc chn mun huy n t phong nay? Ban khng th hoan tac sau khi huy. Ban se khng mt phi huy nu huy n t nay"

    confirmAlert({
      title: 'Xac nhn huy',
      message: message,
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            deleteAvailability(cancelFee,selectedReservation);
          }
        },
        {
          label: 'No',
          onClick: () => {
            // Khng lm g c
          }
        }
      ]
    });


  }

  // bo unavailabledates trong mi phong t
  const deleteAvailability = async (cancelFee,selectedReservation) => {
    setIsSending(true)
    let hasError = false;
    // console.log(roomNumbersId)
    try {

      // y available
      for (const roomTypeId of selectedReservation.roomTypeIdsReserved) {
        for (var i = 0; i < roomTypeId.quantity; i++) {
          try {
            const res = await axios.put(`/rooms/cancelAvailability/${roomTypeId.roomTypeId}`, {
              dates: selectedReservation.allDatesReserve,
              unavailableRangeDates: {
                startDateRange: selectedReservation.start,
                endDateRange: selectedReservation.end
              }
            });
            // console.log(`Room ${roomId} updated successfully.`);
          } catch (err) {
            console.error(err);
            hasError = true;
            return;
          }
        }
      }


      // chinh lai trang thai reservation
      try {
        await axios.put(`/reservation/${selectedReservation._id}`, {
          status: 0,
          cancelDetails:{
            // gi nguyn isAdminCancel, update phi huy theo trng hp
            isAdminCancel:selectedReservation.cancelDetails.isAdminCancel,
            cancelFee:cancelFee
        }
        })
      } catch (err) {
        hasError = true;
        console.log(err)
        return;
      }

      // // gi email xac nhn a huy phong thanh cng
      let emailSubject = "THNG BAO HUY PHONG THANH CNG"
      // try {
      //   const res = await axios.put(`/reservation/email/sendEmailStatusReservation`, {
      //     userId:selectedReservation.userId,
      //     emailSubject:"THNG BAO HUY PHONG THANH CNG",
      //     emailContent:`n t phong ma ${selectedReservation._id} cua quy khach a c huy thanh cng.\n Chi tit n t xem tai trn website`
      //   });
      // } catch (err) {
      //   hasError = true;
      //   console.log(err)
      //   return;
      // }

    } catch (err) {
      console.error('Error:', err);
      hasError = true;
    } finally {
      if (hasError) {
        toast.error(" xy ra li trong qu trnh hy phng.");
      } else {
        toast.success("Hy phng thnh cng");
      }
      setIsSending(false)
      reFetch();
    }

  }


  return (
    <div>
      <Navbar />
      <Header type="list" />
      <div className="listBookingContainer">
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <h1>t phong cua ban</h1>
          <Link style={{ fontSize: '16px', fontStyle: 'italic', marginTop: '5px' }} to="/policy">Xem thm v chinh sach huy</Link>
        </div>

        {loading ? (
          <p>Loading...</p>
        ) : (
          data.map((item, index) => (
            <div key={index} className="flex_div_booking">
              <div className="booking_details">
                <div>Ma t phong: {item._id}</div>
                <div>Ch nghi: {item.hotelName} </div>
                <div>ia chi: {item.hotelAddress}</div>
                <div>Phong t: {item.roomsDetail}</div>
                <div>Tng gia: {new Intl.NumberFormat('vi-VN').format(item.totalPrice * 1000)} VND</div>
                <div style={{ fontWeight: 'bold' }}>Ngay nhn phong:  {new Date(item.start).toLocaleString('vi-VN')}</div>
                <div style={{ fontWeight: 'bold' }}>Ngay tra phong: {subHours(new Date(item.end), 2).toLocaleString('vi-VN')}</div>
                {/* <div>(Thi gian c tinh theo mui gi hin tai may cua ban)</div> */}
                <div style={{ color: item.status === 1 ? 'green' : item.status === 0 ? 'red' : 'blue', fontWeight: 'bold' }}>
                  Tnh trng: {item.status === 1 ? "Thnh cng" : item.status === 0 ? "Hy" : "ang ch"}
                </div>
                {/* xem co yu cu huy t chu ch nghi ko */}
                {item.status ===1 && item.cancelDetails.isAdminCancel ? 
                (
                  <div style={{ fontWeight: 'bold',color:'red',fontStyle:'italic' }}>(n nay ang c yu cu huy t chu ch nghi. <br/>
                   t phong nay vn se co hiu lc cho n khi ban huy. <br/>Ban se khng mt phi huy nu huy n nay)</div>
                ):''}
                <div>Thng tin lin lac ch nghi: {item.hotelContact}</div>



              </div>

              <div style={{ width: '25%', display: 'flex', justifyContent: 'flex-end', flexWrap: 'wrap' }}>
                <button className="cancel_booking" onClick={() => handleCancelReserve(item)}
                  disabled={new Date() > new Date(item.start) || item.status ==0}>
                  {/* disabled={ new Date() > new Date(item.start) || !item.status}> */}

                    {isSending ? 'ang x ly' : 'Huy t phong' }</button> <br />

                {/* <button className="cancel_booking" onClick={() => handleCancelReserve(item.allDatesReserve, item.roomNumbersId, item._id, item.start, item.end, item.roomTypeIdsReserved)}
                >Huy t phong</button>  */}
                <br />
                <div style={{ textAlign: 'right' }}>(Ban se c min phi huy nu huy trong 24h k t luc t hoc trc thi gian nhn phong 3 ngay (trc {subHours(new Date(item.start), 24 * 3).toLocaleString('vi-VN')}))</div>
              </div>

            </div>
          ))
        )}


      </div>

      {/* <Footer /> */}
    </div>
  )
}

export default ListBooking
