import React from 'react'
import "./modifyHotel.css"
import Sidebar from '../../../components/adminComponents/sidebar/Sidebar'
import NavbarAdmin from '../../../components/adminComponents/navbarAdmin/NavbarAdmin'
import { Link, useLocation, useNavigate } from "react-router-dom";
import axios from "axios";
import { useEffect, useState, useContext } from "react";
import useFetch from '../../../hooks/useFetch';
import { AuthContext } from '../../../context/AuthContext';
import DriveFolderUploadOutlinedIcon from "@mui/icons-material/DriveFolderUploadOutlined";
import { hotelInputs, hotelFacilities } from '../../../formSource';
import { toast } from 'react-toastify';
import { jwtDecode } from "jwt-decode";
import { listProvinces } from "../../../listObject";

// css t new Hotel
const ModifyHotel = () => {
    const location = useLocation();
    const idHotel = location.pathname.split("/")[3];
    const { data, loading, error } = useFetch(`/hotels/find/${idHotel}`);
    // console.log(data)
    const [info, setInfo] = useState(data);
    const [files, setFiles] = useState("");
    const [selectedFacilities, setSelectedFacilities] = useState([]);
    const [customFacilities, setCustomFacilities] = useState("");
    const [isSending, setIsSending] = useState(false);

    // const token = document.cookie.replace(/(?:(?:^|.*;\s*)access_token\s*=\s*([^;]*).*$)|^.*$/, "$1");
    // const decodedToken = jwtDecode(token);
    const { user } = useContext(AuthContext) // {user._id}
    // dung  select default type ch nghi
    const defaultType = data.type;
    const navigate = useNavigate()
    const previousPath = location.state?.previousPath;
    if (previousPath !== '/admin/hotels') {
        navigate('/admin/hotels');
    }
    const removeImage = (index) => {
        const newFiles = files.filter((_, i) => i !== index);
        setFiles(newFiles);
    };
    useEffect(() => {
        if (data) {
            setInfo(data);
            // ly facilities cua hotel hin tai cho vao cac bin phu hp
            const initialSelectedFacilities = data.facilities?.filter(facility => hotelFacilities?.includes(facility));
            const initialCustomFacilities = data.facilities?.filter(facility => !hotelFacilities?.includes(facility)).join(", ");
            setSelectedFacilities(initialSelectedFacilities);
            setCustomFacilities(initialCustomFacilities);
        }
    }, [data]);

    const handleChange = (e) => {
        const { id, value } = e.target;
        setInfo((prev) => ({
            ...prev,
            [id]: id === "city" ? value.trim() : value,
        }));
    };
    // validate input ko rng hoc 1 s trng hp ko hp l
    const validateInputs = () => {
        // Check if all hotelInputs are filled
        for (let input of hotelInputs) {
            if (!document.getElementById(input.id).value || !document.getElementById("type").value) {
                return false;
            }
        }
        // Check if description is filled
        if (!document.getElementById("desc").value.trim() || (!customFacilities && selectedFacilities.length == 0)
        ) {//|| (files.length === 0) ko cn vi y bn chinh sa a co anh trc ri
            return false;
        }
        return true;
    };
    // chinh check box facilities
    const handleCheckboxChange = (facility) => {
        setSelectedFacilities((prevSelected) => {
            // nu ngi dung tich cai a co - tc bo no i thi bo no khoi mang
            if (prevSelected.includes(facility)) {
                return prevSelected.filter((item) => item !== facility);
            } else {
                return [...prevSelected, facility];
            }
        });
    };
    // facilities t nhp
    const handleCustomFacilitiesChange = (event) => {
        setCustomFacilities(event.target.value);
    };

    // console.log(info)

    const handleClick = async (e) => {
        setIsSending(true)
        e.preventDefault();
        // ly facilities t checkbox va ngi dung nhp
        const customFacilitiesArray = customFacilities.split(',').map(item => item.trim()).filter(item => item);
        const allSelectedFacilities = [...selectedFacilities, ...customFacilitiesArray];
        try {
            if (allSelectedFacilities.length > 12) {
                toast.error("Chi c chon ti a 12 c s vt cht");
                return;
            }

            if (!validateInputs()) {
                toast.error("Ban cha in u cac thng tin!.");
                return;
            }
            try {
                // list cha link cac photo  y vao api
                const list = await Promise.all(
                    Object.values(files).map(async (file) => {
                        const data = new FormData();
                        data.append("file", file);
                        const uploadRes = await axios.post(
                            `/closedRoom/upload/uploadImage`,
                            data
                        );

                        const { url } = uploadRes.data;
                        return url;
                    })
                );

                const newModifyHotel = {
                    ...info,
                    ...(list.length > 0 && { photos: list }),// nu ngi dung co thm anh vao thi set lai anh mi, ko thi gi nguyn
                    facilities: allSelectedFacilities
                };

                // goi api thay i
                const Success = await axios.put(`/hotels/${idHotel}`, newModifyHotel);
                if (Success) {
                    setIsSending(false)
                    toast.success('Thnh cng chinh sa!');
                    navigate("/admin/hotels");
                } else {
                    toast.error("Error.Please try again");
                    setIsSending(false)
                }
            } catch (err) {
                console.log(error);
            }

        } catch (err) {
            console.log(error);
        } finally {
            setIsSending(false); // Kt thc x l, tr li trng thi ban u cho nt
        }
    };
    return (
        <div className="listAdmin">
            <Sidebar />
            <div className="listContainerAdmin">
                <NavbarAdmin />
                {/* ly theo css cua newHotel.css */}
                <div className="listHotelAdminContainer">
                    <div className="top">
                        <h1 style={{ fontWeight: 'bold' }}>Chinh sa thng tin</h1>

                    </div>

                    <div className="bottom">
                        <div className="left">
                            {files.length > 0 ? (
                                files.map((file, index) => (
                                    <div key={index} style={{ position: 'relative', display: 'inline-block' }}>
                                        <img
                                            src={URL.createObjectURL(file)}
                                            alt={`Uploaded image ${index + 1}`}
                                            style={{ display: 'block' }}
                                        />
                                        <button
                                            onClick={() => removeImage(index)}
                                            style={{
                                                position: 'absolute',
                                                top: '5px',
                                                right: '5px',
                                                backgroundColor: 'red',
                                                color: 'white',
                                                border: 'none',
                                                borderRadius: '50%',
                                                width: '20px',
                                                height: '20px',
                                                cursor: 'pointer',
                                                textAlign: 'center',
                                            }}
                                        >
                                            X
                                        </button>
                                    </div>
                                ))
                            ) : (
                                <div className="no-image-container">
                                    {data.photos?.map((photo, index) => (
                                        <img key={index} src={photo} alt={`nh ${index}`} />
                                    ))}
                                </div>
                            )}
                        </div>


                        <div className="right">
                            <div className="right">
                                <form>
                                    <div className="formInput">
                                        <label htmlFor="file">
                                            Up anh (Click vao y  upload lai anh): <DriveFolderUploadOutlinedIcon className="icon" />
                                        </label>
                                        <input
                                            type="file"
                                            id="file"
                                            multiple
                                            onChange={(e) => setFiles([...e.target.files])}
                                            style={{ display: "none" }}
                                        />
                                    </div>

                                    <div className="formInput" key='type'>
                                        <label>Chon loai ch nghi</label>
                                        <select
                                            id='type'
                                            onChange={handleChange}
                                            value={info.type}
                                        >
                                            <option value={defaultType} >{defaultType}</option> {/* Option mc nh */}
                                            <option value="Khach san" hidden={defaultType === "Khach san"}>Khach san</option> {/* Cc option ca dropdown */}
                                            <option value="Cn h" hidden={defaultType === "Cn h"}>Cn h</option>
                                            <option value="Bit th" hidden={defaultType === "Bit th"}>Bit th</option> {/* Cc option ca dropdown */}
                                            <option value="Resort" hidden={defaultType === "Resort"}>Resort</option>
                                            <option value="Nha nghi" hidden={defaultType === "Nha nghi"}>Nha nghi</option>

                                        </select>
                                    </div>
                                    {/* tinh thanh cho nghi */}

                                    <div className="formInput" key='type'>
                                        <label>Tinh thanh ch nghi</label>
                                        <select
                                            id='city'
                                            onChange={handleChange}
                                            value={info.city}
                                        >
                                            {listProvinces.map((province, index) => (
                                                <option key={index} value={province.name}>
                                                    {province.name}
                                                </option>
                                            ))}

                                        </select>
                                    </div>
                                    {/* render cac trng */}
                                    {hotelInputs.slice(0, 1).concat(hotelInputs.slice(2)).map((input) => (
                                        <div className="formInput" key={input.id}>
                                            <label> {input.label} {info?.type ? info.type.toLowerCase() : "ch nghi"}</label>
                                            <input
                                                id={input.id}
                                                onChange={handleChange}
                                                type={input.type}
                                                value={info[input.id]}
                                            />
                                        </div>
                                    ))}
                                    {/* test */}
                                    <label>M ta ch nghi cua ban(Ti a 1000 ky t) </label>
                                    <textarea
                                        id="desc"
                                        rows="10" /* S dng mc nh hin th ban u */
                                        maxLength="1000"
                                        onChange={handleChange}
                                        style={{ width: "100%", padding: "10px", fontSize: "16px", border: "1px solid #ccc", borderRadius: "5px", boxSizing: "border-box" }}
                                        placeholder={data.desc}
                                        value={info.desc}
                                    ></textarea>

                                    <div className="hotelFacilities">
                                        <div style={{ marginBottom: '10px' }}>C s vt cht ch nghi </div>
                                        {hotelFacilities.map((facility) => (
                                            <div style={{ marginBottom: "10px" }} key={facility}>
                                                <label>
                                                    <input
                                                        type="checkbox"
                                                        value={facility}
                                                        onChange={() => handleCheckboxChange(facility)}
                                                        checked={selectedFacilities?.includes(facility)}
                                                    />
                                                    {facility}
                                                </label>
                                            </div>
                                        ))}

                                        {/* t nhp */}
                                        <div className="customFacilities">
                                            <label>Nhp c s vt cht khc (ngn cch bng du phy): </label>
                                            <input style={{ width: "100%" }}
                                                type="text"
                                                value={customFacilities}
                                                placeholder="Vi du:Thu xe ap, Dich vu phong"
                                                onChange={handleCustomFacilitiesChange}
                                            />
                                        </div>
                                    </div>



                                    <button onClick={handleClick} disabled={isSending}>
                                        {isSending ? 'ang lu...' : 'Lu thng tin ch nghi'}
                                    </button>
                                </form>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ModifyHotel
