import React from 'react'
import './hotelStatistics.css'
import Sidebar from '../../../components/adminComponents/sidebar/Sidebar'
import NavbarAdmin from '../../../components/adminComponents/navbarAdmin/NavbarAdmin'
import { Link, useLocation , useNavigate } from "react-router-dom";
import Highcharts from 'highcharts';
import axios from 'axios';
import useFetch from '../../../hooks/useFetch';
import { useEffect, useState,  } from 'react';
const HotelStatistics = () => {
  const location = useLocation();
  const hotelId = location.pathname.split("/")[4];
  const [month, setMonth] = useState(0);
  const { data, loading, error, reFetch } = useFetch(
    `/reservation/getRevenue/${hotelId}?month=${month}`);
  const { data: hotelDataByMonth, loading: loadinghotelDataByMonth, error: errorhotelDataByMonth, reFetch: reFetchhotelDataByMonth }
    = useFetch(`/reservation/getRevenueByMonths/${hotelId}`);
    console.log(hotelDataByMonth)
   
    const navigate = useNavigate()
    const previousPath = location.state?.previousPath;
    if (previousPath !== '/admin/hotels') {
      navigate('/admin/hotels');
    }
    const handleMonthChange = (event) => {
      setMonth(parseInt(event.target.value));
    };
  // Hm nh dng tin t
  const formatCurrency = (value) => {
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(value * 1000);
  };
  useEffect(() => {
    // biu  ng

    Highcharts.chart('column_revenueByMonth', {
      chart: {
        type: 'line' // Chuyn i sang loi biu  ng
      },
      title: {
        text: 'Biu  doanh thu 6 thang gn nht',
        style: {
          fontWeight: "bold"
        }
      },
      xAxis: {
        categories: hotelDataByMonth.map(item => item.month + "/" + item.year).reverse(),// ly month v o ngc mng categories trc tip
        title: {
          text: 'Danh thu (c tinh theo tng gia tri tt ca n t phong. Thi gian c tinh theo ngay check in cua n).'
        },
      },
      yAxis: {
        title: {
          text: 'Doanh thu',

        },
        labels: {
          formatter: function () {
            return formatCurrency(this.value); // Gi hm formatCurrency  nh dng tin t
          }
        }
      },
      tooltip: {
        formatter: function () {
          return 'Doanh thu: ' + formatCurrency(this.y); // nh dng gi tr tooltip thnh tin t
        }
      },
      credits: {
        enabled: false // Tt ch bn quyn
      },
      accessibility: {
        enabled: false
      },
      series: [{
        name: 'Doanh thu',
        data: hotelDataByMonth.map(item => item.revenue).reverse()
      }]



    });

    if (!loading) {
      const soldRoomsData = data.soldRooms;
      console.log(soldRoomsData)
      const pieChartData = [];
      for (const roomType in soldRoomsData) {  // roomType se lp Deluxve VIp, ...
        if (soldRoomsData.hasOwnProperty(roomType)) {
          pieChartData.push({
            name: roomType,
            y: soldRoomsData[roomType]
          });
        }
      }

      Highcharts.chart('pieChart-container', {
        chart: {
          type: 'pie'
        },
        title: {
          text: 'Ty l s lng loai phong a ban',
          style: {
            fontWeight: "bold"
          }
        },
        credits: {
          enabled: false
        },

        series: [{
          name: 'S lng a ban',
          data: pieChartData
        }]
      });

    }
  }, [hotelDataByMonth, data]);

  return (
    //   css t adminHome.css
    <div className="listAdmin">
      <Sidebar />
      <div className="listContainerAdmin">
        <NavbarAdmin />

        <div className="hotelStatisticContainer">
          <div style={{ fontSize: '30px', fontWeight: 'bold', marginBottom: '10px' }}>Tng quan ch nghi</div>

          <select value={month} onChange={handleMonthChange}>
            <option value={0}>Tt c </option>
            <option value={1}>Trong thng ny </option>
            <option value={-1}>Trong thng trc</option>
          </select>

          <div style={{marginBottom:'20px', fontSize: '14px', fontStyle: 'italic'}}>(i vi la chon tt ca va trong thang nay se bao gm ca nhng n sp ti, 
          c tinh theo ngay check-in cua n. Tng doanh thu bao gm ca phi huy cua nhng n huy co phi)</div>

          <div className="overviewStatistic">
            <div className="overviewStatistic_card">
              <div style={{ fontSize: '15px' }}>Tng doanh thu (VND)</div>
              <div style={{ fontSize: '10px' }}></div>
              <div style={{ fontWeight: 'bold', fontSize: '30px' }}>
                {new Intl.NumberFormat('vi-VN').format(data.totalRevenue * 1000)}</div>
            </div>

            <div className="overviewStatistic_card">
              <div style={{ fontSize: '15px' }}>Tng s khach</div>
              <div style={{ fontWeight: 'bold', fontSize: '30px' }}>{data.totalGuests}</div>
            </div>

            <div className="overviewStatistic_card">
              <div style={{ fontSize: '15px' }}>Tng s n t thanh cng</div>
              <div style={{ fontWeight: 'bold', fontSize: '30px' }}>{data.totalOrders}</div>
            </div>
          </div>

          {/* <div style={{ fontSize: '15px' }}>Loai phong ban chay nht: {data.maxSoldRoomType} ({data.maxSoldRoomCount} a ban)</div> */}
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <div id="column_revenueByMonth" style={{ flex: 1, width: '48%', height: '400px', marginTop: '50px' }}></div>

            <div id="pieChart-container" style={{ flex: 1, width: '48%', height: '400px', marginTop: '50px', marginLeft: '50px' }}></div>
          </div>



        </div>
      </div>
    </div>

  )
}

export default HotelStatistics
