import React from 'react'
import './modifyRoomCount.css'
import Sidebar from '../../../components/adminComponents/sidebar/Sidebar'
import NavbarAdmin from '../../../components/adminComponents/navbarAdmin/NavbarAdmin'
import { Link, useLocation } from "react-router-dom";
import axios from "axios";
import { useEffect, useState, useContext, } from "react";
import useFetch from '../../../hooks/useFetch';
import { AuthContext } from '../../../context/AuthContext';
import { roomInputs } from '../../../formSource';
import { toast } from 'react-toastify';
import { useNavigate } from "react-router-dom";
const ModifyRoomCount = () => {
    const location = useLocation();
    const idRoom = location.pathname.split("/")[5];
    const { data: roomTypeData, loading, error, reFetch } = useFetch(`/rooms/find/${idRoom}`);
    const [roomCountToAdd, setRoomCountToAdd] = useState(0);
    const [roomCountToDelete, setRoomCountToDelete] = useState(0);
    const navigate = useNavigate()
    const previousPath = location.state?.previousPath;
    if (previousPath !== '/admin/rooms/smallRoomDetails') {
        navigate('/admin/rooms');
    }
    const handleCloseRoom = async (roomId, currentStatus) => {
        // console.log("Room ID to delete:", roomId);
        // Thm logic x l xa phn t  y
        try {
            // neu trang thai hien tai la true, tc ang m va ngi dung mun ong
            if (currentStatus) {
                for (const room of roomTypeData.roomNumbers) {
                    // nu vn con loai phong xoa c
                    if (canDelete(room)) {
                        toast.error("Nu ban mun giam s lng phong ban,hay xoa ht phong trc thay vi ong phong")
                        return;
                    }
                }

            }
            
            const res = await axios.put(`/rooms/changeStatusRoomInRoomType/${roomId}`)

            if (res.status === 200) {
                toast.success("Chinh thanh cng") // In ra thng bo nu thnh cng
                reFetch();
            }
        } catch (error) {
            toast.error("Co li xay ra vui long th lai")
            console.log(error)
        }
    };

    // kim tra xem xoa c phong ko
    const canDelete = (roomNumber) => {
        if (!roomNumber.unavailableDates || roomNumber.unavailableDates.length === 0) {
            // Nu khng c unavailableDates hoc mng rng, cho php xa
            return true;
        }
        // Lp qua tt c cc phn t trong mng unavailableDates
        for (const date of roomNumber.unavailableDates) {
            //VI DU HM NAY 1/5 14H10 THI XOA C PHONG NHO CO N NGAY 1/5 (>14H)
            // Kim tra nu ngy hin ti khng ln hn mi phn t trong mng
            if (new Date() <= new Date(date)) {
                // 14h GMT
                return false; // Nu c t nht mt ngy trong tng lai, khng cho php xa
            }
        }
        return true; // Nu ngy hin ti ln hn tt c cc ngy trong mng, cho php xa
    };

    const handleRoomCountToAddChange = (event) => {
        const count = parseInt(event.target.value);
        setRoomCountToAdd(count);
    };
    const handleRoomCountToDeleteChange = (event) => {
        setRoomCountToDelete(parseInt(event.target.value));
    };

    // ham submit
    const submitAddRoom = async (event) => {
        try {
            const res = await axios.put(`/rooms/addRoomToRoomType/${idRoom}`, {
                roomCountToAdd: roomCountToAdd
            });
            if (res.status === 200) {
                toast.success("Thm thanh cng") // In ra thng bo nu thnh cng
                reFetch(); // Thc hin li fetch d liu nu cn
            }

        } catch (error) {
            console.log(error);
        }
        setRoomCountToAdd(0)
    };

    const submitDeleteRoom = async (event) => {
        try {
            const res = await axios.put(`/rooms/deleteRoomInRoomType/${idRoom}`, {
                roomCountToDelete: roomCountToDelete
            });

            if (res.status === 200) {
                toast.success("Xoa thanh cng") // In ra thng bo nu thnh cng
                reFetch(); // Thc hin li fetch d liu nu cn
            }
        } catch (error) {
            toast.error(error.response.data.message)
            reFetch();
            console.log(error);
        }
        setRoomCountToDelete(0)
    };
    return (
        <div className="listAdmin">
            <Sidebar />
            <div className="listContainerAdmin">
                <NavbarAdmin />

                <div className="ModifyRoomCountContainer">

                    <div style={{ fontSize: '20px', fontWeight: 'bold', marginBottom: '20px' }}>Loai phong: {roomTypeData.title} (tng s lng phong: {roomTypeData.roomNumbers?.length})</div>
                    <div className="modifyRoomCount">
                        <label style={{ display: 'flex', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
                            Nhp lng phng cn thm (tng):
                            <input
                                type="number"
                                value={roomCountToAdd}
                                onChange={handleRoomCountToAddChange}
                                min={1}
                            />
                        </label>
                        <button style={{ backgroundColor: 'rgb(91, 248, 0)' }} className="modifyRoomCount_btn" onClick={submitAddRoom}>Thm</button>
                        <label style={{ display: 'flex', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
                            Nhp lng phng cn xa (giam):
                            <input
                                type="number"
                                value={roomCountToDelete}
                                onChange={handleRoomCountToDeleteChange}
                                max={roomTypeData.roomNumbers?.length} 
                                min={1}
                            />
                        </label>
                        <button style={{ backgroundColor: 'red' }} className="modifyRoomCount_btn" onClick={submitDeleteRoom}>Xoa</button>
                    </div>

                    {/* <div style={{ fontStyle: 'italic', marginBottom: '10px' }}>(Vi nhng phong a co n t phong hoc n ong phong trong thi gian ti se hin mau o,
                        khng th xoa c. Trong trng hp o,nu ban mun giam s lng phong mun ban, ban s dung chc nng ong phong.
                      )</div> */}
                       <div style={{ fontStyle: 'italic', marginBottom: '10px' }}>(Vi nhng phong a co n t phong hoc n ong phong trong thi gian ti se hin mau o,
                        khng th xoa c (S lng phong o la s lng phong ti thiu cn gi). 
                        Trong trng hp o, nu ban mun giam s lng phong, ban co th yu cu huy n cua khach hoc s dung chc nng ong phong. 
                       
                      )</div>
                    {/*  danh sach phong */}
                    <div style={{ backgroundColor: '#ccc' }} className="roomNumberContainer">
                        <div className="roomNumber">STT</div>
                        <div className="roomNumber">ID phong</div>
                        {/* <div className="roomNumber">Trang thai</div> */}
                        <div style={{ width: '20%' }} className="roomNumber" >Tinh trang</div>
                    </div>
                    {roomTypeData.roomNumbers?.map((roomNumber, index) => (
                        <div key={index} className="roomNumberContainer" style={{ backgroundColor: canDelete(roomNumber) ? '' : 'red' }}>

                            <div className="roomNumber">{index + 1}</div>
                            <div className="roomNumber" style={{ fontWeight: 'unset' }}>{roomNumber._id}</div>
                            {/* <div className="roomNumber" style={{ color: roomNumber.status ? 'green' : 'yellow' }}>
                                {roomNumber.status ? 'M' : 'ng'}
                            </div> */}
                            {/* nu ko xoa c  */}
                            {!canDelete(roomNumber) ? (
                                <div className="roomNumber">
                                    {/* <button className="roomNumber_btn" onClick={() => handleCloseRoom(roomNumber._id, roomNumber.status)}>ong/M</button> */}
                                    a t hoc ong
                                </div>

                            ) : (
                                <div className="roomNumber">
                                </div>
                            )}

                        </div>
                    ))}

                </div>
            </div>
        </div>
    )
}

export default ModifyRoomCount
