import React from 'react'
import "./statusTransaction.css"
import Navbar from "../../../components/navbar/Navbar";
import Header from "../../../components/header/Header";

const StatusTransaction = ({ status }) => {
    return (
        <div>
            <Navbar />
            <Header type="list" />
            <div style={{display:'flex',justifyContent:'center',flexDirection:'column',alignItems:'center'}} className="statusTransactionContainer">
                {status === 'success' ? (
                    <>
                        <h1 style={{ color: 'green' }}>t phng thnh cng</h1>
                        <button  style={{width:'20%'}} onClick={() => { window.location.href = '/bookings' }}>Xem n t phong cua ban</button>
                    </>
                ) : status === 'fail' ? (
                    <>
                        <h1 style={{ color: 'red' }}>t phng tht bi</h1>
                        <button onClick={() => { window.location.href = '/' }}>Quay v trang ch</button>
                    </>
                ) : (
                    <h1>Trng thi khng hp l</h1>
                )}
            </div>
        </div>
    )
}

export default StatusTransaction
