import { format,addDays,subDays   } from "date-fns";
import React from 'react';

export const roomColumns = [
    { field: "_id", headerName: "ID", width: 100, hide:true },
    {
      field: "title",
      headerName: "Tn loai phong (loai cn h, bit th,...) ",
      // width: 200,
      align:'center',
      headerAlign:'center',
      flex:1.25,
      renderHeader: (params) => (
        <div style={{ whiteSpace: 'normal', textAlign:'center',lineHeight: '1.2' }}>
          {params.colDef.headerName}
        </div>
      )
      
    },
    {
      field: "desc",
      headerName: "M ta ",
      flex:2,
      // width: 330,
      cellClassName: 'wrap-content', // listReservation.css
      // align:'center',
      headerAlign:'center'
    },
    // {
    //   field: "hotelName",
    //   headerName: "Hotel",
    //   width: 200,
    // },
    {
      field: "price",
      headerName: "Gia mi m(VND)",
      // width: 140,
      flex:1,
      valueFormatter: (params) => {
            const formattedValue = new Intl.NumberFormat('vi-VN').format(params.value*1000)
            return `${formattedValue}`;
    },
      align:'center',
      headerAlign:'center'

    },
    {
      field: "maxPeople",
      headerName: "S ngi",
      // width: 130,
      flex:0.5,
      align:'center',
      headerAlign:'center'
    },
    {
      field: "roomCount",
      headerName: "S lng",
      flex:0.5,
      // width: 150,
      align:'center',
      headerAlign:'center',
      renderCell: (params) => {
          const roomNumbers = params.row.roomNumbers || [];
          return roomNumbers.length;
      }
    },
 
  ];



  const formatDate = (date) => {
    let dateFormat = new Date(date)
    // const [month, day, year] = dateFormat.toLocaleDateString().split('/');
    return dateFormat.toLocaleDateString('vi-VN');
  };


  // ct danh sach reservations
  export const ReservationColumns = [
    { field: "_id", headerName: "Ma t phong",flex:1, width: 140 },
    {
      field: "start",
      headerName: "Check-in",
      flex:1,
      width: 90,
      valueFormatter: (params) => formatDate(params.value),
      headerAlign: 'center',
      align:'center',
      comparator: (a, b) => new Date(a).getTime() - new Date(b).getTime()

    },
    {
      field: "end",
      headerName: "Check-out",
      flex:1,
      width: 90,
      valueFormatter: (params) => formatDate(params.value),
      headerAlign: 'center',
      align:'center'
      
    },
    { field: "hotelName", headerName: "Ch nghi",flex:2, width: 180, headerAlign: 'center',  align:'center' },

    {
      field: "roomsDetail",
      headerName: "Phong",
      flex:2,
      width: 250,
      cellClassName: 'wrap-content', // xung dong khi ni dung dai
      headerAlign: 'center',
      align:'center',
      renderCell: (params) => {
        const roomDetails = params.value;
        return (
          <div className="wrap-content">
            {roomDetails}
          </div>
        );
      }
    },
    
    {
      field: "guest",
      headerName: "S ngi",
      width: 100,
      flex:1,
      cellClassName: 'wrap-content', // xung dong khi ni dung dai
      headerAlign: 'center',
      align:'center',
      renderCell: (params) => {
        const { adult, children } = params.row.guest;
        return `${adult} ngi ln, ${children} tr em`;
      }
    },

    {
      field: "totalPrice",
      headerName: "Tng gia(VND)",
      flex:1,
      width: 100,
      valueFormatter: (params) => {
        if (params.value !== null) {
            const formattedValue = new Intl.NumberFormat('vi-VN').format(params.value*1000)
            return `${formattedValue}`;
        }
        return null; // Tr v null nu gi tr l null
    },
      headerAlign: 'center',
      align:'center'
    },
    // {
    //   field: "createdAt",
    //   headerName: "Thi gian t",
    //   width: 115,
    //   valueGetter: (params) => new Date(params.value).toLocaleDateString('vi-VN'),
    //   headerAlign: 'center',
    //   align:'center'

    // },
    {
      field: "phoneNumber",
      headerName: "S in thoai",
      width: 120,
      headerAlign: 'center',
      align:'center'
    },
    {
      field: "status",
      headerName: "Trang thai",
      width: 100,
      flex:0.75,
      renderCell: (params) => {
        let statusText;
        let statusColor;
      
        switch (params.value) {
          case 1:
            statusText = "OK";
            statusColor = "green";
            break;
          case 0:
            statusText = "HUY";
            statusColor = "red";
            break;
          case -1:
            statusText = "ang ch";
            statusColor = "blue";
            break;
          default:
            statusText = "Khng xc nh";
            statusColor = "black";
            break;
        }
      
        return (
          <span style={{ color: statusColor }}>{statusText}</span>
        );
      },
      headerAlign: 'center',
      align:'center'
    },

  ];
  
  export const RenenueAllHotelColumn =
  // ct ds hotel revenue
  [
    {
      field: "hotelName",
      headerName: "Tn ch nghi",
      width: 350,
      headerAlign: 'center',
      align:'center'
    },
    {
      field: "userOwner",
      headerName: "Email tai khoan chu",
      width: 350,
      headerAlign: 'center',
      align:'center'
    },
    {
      field: "createdAt",
      headerName: "Ngay hoat ng",
      // renderHeader: () => (
      //   <div style={{whiteSpace:'normal'}} className="wrap-Headercontent">
      //     Ngay bt u hoat ng
      //   </div>
      // ),
      width: 115,
      valueFormatter: (params) => {
        const value = params.value ? params.value : '2024-05-20';
        return formatDate(value);
      },      comparator: (a, b) => new Date(a).getTime() - new Date(b).getTime(),
      headerAlign: 'center',
      align:'center'

    },
    {
      field: "totalRevenue",
      headerName: "Tng doanh thu",
      width: 250,
      valueFormatter: (params) => {
        const multipliedValue = params.value * 1000;
        // nh dng gi tr thnh s nguyn
        const formattedValue = new Intl.NumberFormat('vi-VN').format(multipliedValue);
        return `${formattedValue} VND`;
    },
      headerAlign: 'center',
      align:'center'
    },

    {
      field: "commission",
      headerName: "Tin hoa hng",
      width: 150,
      headerAlign: 'center',
      align: 'center',
      valueGetter: (params) => {
          // Ly gi tr ca trng "Tng doanh thu" t params.row
          const totalRevenue = params.row.totalRevenue;
          if (totalRevenue !== undefined && totalRevenue !== null) {
              return totalRevenue * 0.1*1000; // 15% ca gi tr "totalRevenue"
          }
          return null; 
      },
      valueFormatter: (params) => {
          if (params.value !== null) {
              const formattedValue = new Intl.NumberFormat('vi-VN').format(params.value)
              return `${formattedValue} VND`;
          }
          return null; // Tr v null nu gi tr l null
      }
  }
  
  ]

  export const allHotelPaymentColumn=
  [
    {
      field: "idOwnerHotel",
      headerName: "Id tai khoan",
      width: 350,
      headerAlign: 'center',
      align:'center'
    },
    {
      field: "email",
      headerName: "Email tai khoan chu",
      width: 250,
      headerAlign: 'center',
      align:'center'
    },
 
    {
      field: "totalPrice",
      headerName: "Tng doanh thu",
      width: 250,
      valueFormatter: (params) => {
        const multipliedValue = params.value * 1000;
        // nh dng gi tr thnh s nguyn
        const formattedValue = new Intl.NumberFormat('vi-VN').format(multipliedValue);
        return `${formattedValue} VND`;
    },
      headerAlign: 'center',
      align:'center'
    },

    {
      field: "needToPaid",
      headerName: "Cn thanh toan",
      width: 150,
      headerAlign: 'center',
      align: 'center',
      valueGetter: (params) => {
          // Ly gi tr ca trng "Tng doanh thu" t params.row
          const totalRevenue = params.row.totalPrice;
          if (totalRevenue !== undefined && totalRevenue !== null) {
              return totalRevenue * 0.9*1000; // 90% ca gi tr "totalRevenue"
          }
          return null; 
      },
      valueFormatter: (params) => {
          if (params.value !== null) {
              const formattedValue = new Intl.NumberFormat('vi-VN').format(params.value)
              return `${formattedValue} VND`;
          }
          return null; // Tr v null nu gi tr l null
      }
  },

  {
    field: "paymentInfo",
    headerName: "Thng tin thanh toan",
    width: 250,
    cellClassName: 'wrap-content', 
    headerAlign: 'center',
    align: 'center',
  }
  ]