import React from 'react'
import Sidebar from '../../../components/adminComponents/sidebar/Sidebar'
import NavbarAdmin from '../../../components/adminComponents/navbarAdmin/NavbarAdmin'
import "./listRoom.css"
import { DataGrid } from "@mui/x-data-grid";
import { roomColumns } from '../../../datatablesource';
import useFetch from '../../../hooks/useFetch';
import { useContext, useState, useEffect } from 'react'
import { AuthContext } from '../../../context/AuthContext';
import { Link, useLocation, useNavigate } from "react-router-dom";
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import axios from 'axios';
import { toast } from 'react-toastify';
import { jwtDecode } from "jwt-decode";
import { addYears } from "date-fns";
const currentDate = new Date();
const endLessDate = addYears(currentDate, 10)
const ListRoom = () => {
    // const token = document.cookie.replace(/(?:(?:^|.*;\s*)access_token\s*=\s*([^;]*).*$)|^.*$/, "$1");
    // const decodedToken = jwtDecode(token);
    const { user } = useContext(AuthContext) // {user._id}
    const { data: hotelData, loading: hotelLoading, error: hotelError, reFetch: hotelReFetch } = useFetch(`/hotels/getByAdmin`);
    const [hotelId, setHotelId] = useState(hotelData.length > 0 ? hotelData[0]._id : null);
    const { data: roomData, loading: roomLoading, error: roomError, reFetch: roomReFetch } = useFetch(`/rooms/${hotelId}`);
    // ly ra nhng n t phong trong tng lai
    const { data: reservationDataFuture, loading: reservationLoading, error: reservationError,
        reFetch: reservationReFetch } = useFetch(`/reservation/admin/?startDay=${currentDate}&endDay=${endLessDate}&status="success"`);
    // console.log(reservationDataFuture)
    const navigate = useNavigate()
    const location = useLocation();
    // ly hotel id nu ngi dung va tao phong hoc chinh phong xong
    const hotelIdFromAddModifyRoom = location.state?.hotelIdFromAddModifyRoom;
    // console.log(hotelIdFromAddModifyRoom)
    const handleHotelChange = (e) => {
        setHotelId(e.target.value);
    };
    useEffect(() => {
        // nu va thm hoc sa loai phong thi  trang list room se set id hotel ma co loai phong va thm hoc sa, ko thi set id hotel phn t u tin
        if (hotelIdFromAddModifyRoom) {
            setHotelId(hotelIdFromAddModifyRoom)
        } else {
            setHotelId(hotelData[0]?._id)
        }
    }, [hotelData]);
    // console.log(roomData)

    // chuyn hng
    const handleNavigation = (path) => {
        navigate(path, { state: { previousPath: '/admin/rooms', hotelIdFromListRoom: hotelId } });
    };
    const handleDelete = (typeRoomId) => {
        // // Kim tra xem phong y co n sp ti ko
        const hasMatchingTypeRoomId = reservationDataFuture.some(reservation => {
            // Kim tra xem reservation c thuc tnh roomTypeIdsReserved khng
            if (reservation.roomTypeIdsReserved) {
                // Duyt qua mi phn t trong roomTypeIdsReserved  kim tra roomTypeId
                return reservation.roomTypeIdsReserved.some(({ roomTypeId }) => roomTypeId === typeRoomId);
            }
            return false; // Nu khng c roomTypeIdsReserved, khng tha mn iu kin
        });

        // Nu c phn t tha mn iu kin, hin th thng bo li
        if (hasMatchingTypeRoomId) {
            toast.error("Loai phong nay co n t sp ti! Khng th xoa")
            return; // Dng hm
        }
        // xacs nhan xoa
        confirmAlert({
            title: 'Xac nhn',
            message: 'Ban co chc chn mun xoa loai phong nay?',
            buttons: [
                {
                    label: 'Co',
                    onClick: () => {
                        // Xc nhn xa phong
                        deleteRoom(typeRoomId);
                    }
                },
                {
                    label: 'Khng',
                    onClick: () => {
                        // Khng lm g c
                    }
                }
            ]
        });

    }

    const deleteRoom = async (typeRoomId) => {

        try {
            const Success = await axios.delete(`/rooms/${typeRoomId}`);

            if (Success) {
                // Nu xa thnh cng, ti li d liu
                roomReFetch();
                toast.success('Xoa phong thanh cng');
            } else {
                toast.error('Co li xay ra. Vui long th lai');
            }
        } catch (error) {
            console.error('Error ', error);
            toast.error('Co li xay ra. Vui long th lai');
        }
    }

    // ham chinh trang thai loai phong
    const changeStatusRoomType = async (roomTypeId, currentStatus) => {
        confirmAlert({
            title: 'Xac nhn',
            message: 'Ban co chc chn mun chinh trang thai loai phong/cn nay?',
            buttons: [
                {
                    label: 'Co',
                    onClick: async () => {
                        // Xc nhn xa phong
                        try {
                            const Success = await axios.put(`/rooms/${roomTypeId}`, {
                                status: !currentStatus
                            });
                            // Nu thnh cng, ti li d liu
                            roomReFetch();
                            toast.success('Chinh trang thai thanh cng');
                        } catch (error) {
                            console.error('Error ', error);
                            toast.error('Co li xay ra. Vui long th lai');
                        }
                    }
                },
                {
                    label: 'Khng',
                    onClick: () => {
                        // Khng lm g c
                    }
                }
            ]
        });

    }

    // thm ct status va xoa sa
    const actionColumn = [
        {
            field: "status",
            headerName: "Trang thai",
            flex: 1,
            // width: 150,
            align: 'center',
            headerAlign: 'center',
            renderCell: (params) => {
                return (
                    <div style={{
                        color: params.value ? 'green' : 'red',
                        fontWeight: 'bold', cursor: 'pointer'
                    }} onClick={() => changeStatusRoomType(params.row._id, params.row.status)}>
                        {params.value ? 'HOAT NG' : 'DNG'}
                    </div>
                )
            }
        },
        {
            field: "action",
            headerName: "Hanh ng",
            // width: 270,
            flex: 1.75,
            headerAlign: 'center',
            renderCell: (params) => {
                return (
                    <div className="cellAction">
                        <div className="viewButton wrap-content" onClick={() => handleNavigation(`/admin/rooms/${params.row._id}`)}>
                            Chinh thng tin
                        </div>
                        <div className="viewButton wrap-content" onClick={() => handleNavigation(`/admin/rooms/smallRoomDetails/${params.row._id}`)}>
                            Chinh s lng
                        </div>
                        <div
                            className="deleteButton wrap-content"
                            onClick={() => handleDelete(params.row._id)}
                        >
                            Xoa loai phong
                        </div>
                    </div>
                );
            },
        },
    ];
    return (
        <div className="listAdmin">
            <Sidebar />
            <div className="listContainerAdmin">
                <NavbarAdmin />

                <div className="ListRoomAdminContainer">
                    <h2>Phong cua ban</h2>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>

                        <div className="hotelSelectBox">
                            {/* <label>Choose a hotel</label> */}
                            <select
                                id="hotelId"
                                value={hotelId}
                                onChange={handleHotelChange}
                            >
                                <option value="d" disabled selected>Chn ch nghi</option>
                                {hotelLoading
                                    ? "loading"
                                    : hotelData &&
                                    hotelData.map((hotel) => (
                                        <option key={hotel._id} value={hotel._id}>
                                            {hotel.name}
                                        </option>
                                    ))}
                            </select>

                        </div>


                        <button onClick={() => handleNavigation(`/admin/rooms/new`)} style={{
                            fontSize: '14px', backgroundColor: '#5bf800', border: 'none',
                            height: '40px', color: 'white', fontWeight: 'bold', borderRadius: '5px'
                        }}>Thm loai phong (loai ch )</button>
                    </div>


                </div>


                <DataGrid autoHeight getRowHeight={() => 'auto'}
                    className="datagrid"
                    rows={roomData}
                    columns={roomColumns.concat(actionColumn)}
                    pageSize={5}
                    rowsPerPageOptions={[5]}
                    // checkboxSelection
                    getRowId={(row) => row._id}
                    localeText={{
                        noRowsLabel: <span style={{ color: 'red' }}>Ban cha chon ch nghi hoc ch nghi nay cha co phong</span>,
                    }}
                />

                <div style={{ fontStyle: 'italic', width: '95%', margin: 'auto' }}>
                    (Nu ban mun tam dng ban mt loai phong (loai cn h, bit th,.. ) nao o,
                    ban co th bm vao  hin thi trang thai  chinh trang thai
                    . Nhng loai phong  trang thai "DNG" se khng hin thi i vi khach).
                </div>




            </div>
        </div>

    )
}

export default ListRoom
