import React from 'react'
import { useContext } from 'react'
import Sidebar from '../../../components/adminComponents/sidebar/Sidebar'
import NavbarAdmin from '../../../components/adminComponents/navbarAdmin/NavbarAdmin'
import useFetch from '../../../hooks/useFetch';
import { AuthContext } from '../../../context/AuthContext'
import { Link } from 'react-router-dom';
import "./listHotel.css"
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import axios from 'axios';
import { toast } from 'react-toastify';
import { format, addDays, addYears, subYears } from "date-fns";
import { useNavigate } from 'react-router-dom';
import { jwtDecode } from "jwt-decode";
import { useEffect } from 'react';
const currentDate = new Date();
const endLessDate = addYears(currentDate, 10)

const ListHotel = () => {
    // token cha _id tai khoan va isAdmin
    // const token = document.cookie.replace(/(?:(?:^|.*;\s*)access_token\s*=\s*([^;]*).*$)|^.*$/, "$1");
    // const decodedToken = jwtDecode(token);
    // // console.log(decodedToken);
    const { user } = useContext(AuthContext) // {user._id}
    const { data, loading, error, reFetch } = useFetch(
        `/hotels/getByAdmin`);
    // ly ra nhng n t phong trong tng lai
    const { data: reservationData, loading: reservationLoading, error: reservationError,
        reFetch: reservationReFetch } = useFetch(`/reservation/admin/?startDay=${currentDate}&endDay=${endLessDate}&status="success"`);
    // console.log(reservationData)
    const navigate = useNavigate();
    const handleDelete = (hotelId) => {
        const hasMatchingHotelId = reservationData.some(item => item.hotelId === hotelId);

        // Nu c phn t no c hotelId trng khp, thot ra khi hm
        if (hasMatchingHotelId) {
            toast.error("Ch nghi nay ang co n t phong sp ti, khng th xoa!")
            return;
        }
        confirmAlert({
            title: 'Confirm',
            message: 'Ban co chc chn mun xoa ch nghi nay?',
            buttons: [
                {
                    label: 'Yes',
                    onClick: () => {
                        // Xc nhn xa khch sn
                        deleteHotel(hotelId);
                    }
                },
                {
                    label: 'No',
                    onClick: () => {
                        // Khng lm g c
                    }
                }
            ]
        });
    };


    // ham chuyn hng
    const handleEditHotel = (itemId) => {
        navigate(`/admin/hotels/${itemId}`, { state: { previousPath: '/admin/hotels' } });
    };
    const handleStatisticHotel = (itemId) => {
        navigate(`/admin/hotels/revenue/${itemId}`, { state: { previousPath: '/admin/hotels' } });
    };
    const deleteHotel = async (hotelId) => {
        try {
            // Gi yu cu xa khch sn n my ch

            const Success = await axios.delete(`/hotels/${hotelId}`);

            if (Success) {
                // Nu xa thnh cng, ti li d liu
                reFetch();
                toast.success('Xoa thanh cng');
            } else {
                toast.error('Co li xay ra vui long th lai');
            }
        } catch (error) {
            console.error('Error deleting hotel:', error);
            toast.error('Co li xay ra vui long th lai');
        }
    };

    return (
        <div className="listAdmin">
            <Sidebar />
            <div className="listContainerAdmin">
                <NavbarAdmin />

                <div className="listHotelAdminContainer">
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <h1>Ch nghi cua ban</h1>
                        <Link to={`/admin/hotels/new`}>
                            <button className="addHotel_btn" >Thm ch nghi mi</button>
                        </Link>
                    </div>

                    {loading ? (
                        "loading"
                    ) : (

                        <>
                            {data.length === 0 ? (
                                <div>Bn cha c ch ngh no. Hay thm ch nghi bng cach n nut "Thm ch nghi"</div>
                            ) : (
                                data.map((item) => (
                                    <div className="listHotelAdmin" key={item._id}>
                                        <img src={item.photos[0]} alt="" className="siImg" />
                                        <div className="siDesc">
                                            <h1 className="siTitle">{item.name}</h1>
                                            <span className="siDistance">Khoang cach n trung tm: {item.distance}m t trung tm</span>

                                            <span className="siFeatures">ia chi ch nghi: {item.address}</span>

                                        </div>
                                        <div className="listHotel_btn">
                                            {/* <span className="siTaxOp">Cho {options.adult} ngi, {days} m</span> */}
                                            {/* <Link to={`/hotels/${item._id}`}>
                                            </Link> */}
                                            <button
                                                onClick={() => handleStatisticHotel(item._id)}
                                            >Thng k s liu </button>

                                            <button onClick={() => handleEditHotel(item._id)} >
                                                Chinh sa thng tin
                                            </button>
                                            <button style={{backgroundColor:'red'}} onClick={() => handleDelete(item._id)}>Xoa ch nghi</button>

                                        </div>
                                    </div>
                                )))}
                        </>
                    )}
                </div>


            </div>
        </div>
    )
}

export default ListHotel
