import { createContext, useReducer } from "react";
import React from "react";
import { format,addDays,addHours  } from "date-fns";
const utc = new Date().getTimezoneOffset() / 60 //-7
// nu qua 14h thi mc inh   khoang ngay la ngay ti + ngay tip theo
const currentDate = addHours(new Date(),10+(-7-utc));
currentDate.setHours(0, 0, 0, 0);
const startDate = addHours(currentDate, 7 - utc);
const INITIAL_STATE = {
  destination: "Ha Ni",
  dates: [
    {
      // nu may  mui gi VN thi ang la 14h
      startDate: startDate,
      endDate: addDays(startDate, 1),
      key: "selection",
    },
  ],
  options: {
    adult: 2,
    children: 0,
    room: 1,
  },
};

export const SearchContext = createContext(INITIAL_STATE);

const SearchReducer = (state, action) => {
  switch (action.type) {
    case "NEW_SEARCH": // MOI KHI thay i thng tin thanh search
      return action.payload; // GM CITY, DATE RANGE, OPTION
    case "RESET_SEARCH":
      return INITIAL_STATE;
    default:
      return state;
  }
};

export const SearchContextProvider = ({ children }) => {
  const [state, dispatch] = useReducer(SearchReducer, INITIAL_STATE);

  return (
    <SearchContext.Provider
      value={{
        destination: state.destination,
        dates: state.dates,
        options: state.options,
        dispatch,  // dispatch khi mun dung SearchReducer
      }}
    >
      {children}
    </SearchContext.Provider>
  );
};