import React from 'react'
import "./modifyUser.css"
import { useState, useContext } from "react";
import Sidebar from '../../../components/adminComponents/sidebar/Sidebar'
import NavbarAdmin from '../../../components/adminComponents/navbarAdmin/NavbarAdmin'
import { toast } from 'react-toastify';
import axios from 'axios';
const ModifyUser = ({ modify }) => {
    const [credentials, setCredentials] = useState({
        oldPassword: undefined,
        newPassword: undefined,
        newPasswordRepeat: undefined // Thm trng passwordRepeat vo state, trng nay khi y vao API ko dung
    });
    const handleChange = (e) => {
        setCredentials((prev) => ({ ...prev, [e.target.id]: e.target.value }));
    };
    console.log(credentials)
    const handleChangePassword = async (e) =>{
        e.preventDefault();
        if ((credentials.newPassword !== credentials.newPasswordRepeat) || !credentials.newPassword || !credentials.oldPassword) {
            toast.error("Mt khu khng khp hoc ban cha in u t");
            return;
          }
          try{
            const res = await axios.post("/auth/changePassword", credentials);
            if(res.status===200){
                toast.success("i mt khu thanh cng")
            } 
          }catch(error){
              console.log(error)
          }
    }
    return (
        <div className="listAdmin">
            <Sidebar />
            <div className="listContainerAdmin">
                <NavbarAdmin />

                {/*  doi mat khau */}
                {modify === 'changePassword' ? (
                <div className="modifyUserContainer">
                    <h1>i mt khu</h1>
                    <div className="modifyUserContainer_changePassword">
                        <input
                            type="password"
                            placeholder="Nhp mt khu cu"
                            id="oldPassword"
                            onChange={handleChange}
                            
                        />
                        <input
                            type="password"
                            placeholder="Nhp mt khu mi"
                            id="newPassword"
                            onChange={handleChange}
                            
                        />

                        <input
                            type="password"
                            placeholder="Nhp lai mt khu mi"
                            id="newPasswordRepeat"
                            onChange={handleChange}
                            
                        />

                        <button onClick={handleChangePassword}>i mt khu</button>
                    </div>
                </div>

                // update payment info
                ) : modify === 'updatePaymentInfo' ? (
                    <>
                        <h1>dfdsf</h1>
                    </>
                ) : (
                    <h1>Trng thi khng hp l</h1>
                )}




            </div>
        </div>

    )
}

export default ModifyUser
