import React from 'react'
import axios from "axios";
import { AuthContext } from '../../context/AuthContext';
import useFetch from '../../hooks/useFetch';
import "./listRoomClient.css"
import { useContext, useState, useEffect } from "react";
import { SearchContext } from '../../context/SearchContext';
import { format, addDays, subDays, addHours } from "date-fns";
import { DateRange } from "react-date-range";
import {
  faBed,
  faCalendarDays,
  faPerson,
} from "@fortawesome/free-solid-svg-icons";
import {
  faCircleArrowLeft,
  faCircleArrowRight,
  faCircleXmark,
  faLocationDot,
} from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
const ListRoomClient = ({ hotelId, hotelType }) => {
  const timeZone = new Date().getTimezoneOffset() / 60  //  lch mui gi so vi UTC cua may hin tai
  // cac room type ang hoat ng cua ch nghi hin tai
  const { data, loading, error, reFetch } = useFetch(`/rooms/${hotelId}/?status=true`);
  const [selectedRooms, setSelectedRooms] = useState([]);
  const searchContext = useContext(SearchContext);
  const [dates, setDates] = useState(searchContext.dates);
  // dates[0].startDate.setHours(14, 0, 0, 0);
  // dates[0].endDate.setHours(14, 0, 0, 0);
  const [options, setOptions] = useState(searchContext.options);
  const [destination, setDestination] = useState(searchContext.destination);
  const [openDate, setOpenDate] = useState(false);
  // const [expandedPhotoIndex, setExpandedPhotoIndex] = useState(null); // State  lu index ca nh ang c phng to
  const [openExpandPhoto, setOpenExpandPhoto] = useState(false);
  const [slideNumber, setSlideNumber] = useState(0);
  const [indexItem, setIndexItem] = useState(0);
  const { dispatch } = useContext(SearchContext);
  const [selectedRoomIds, setSelectedRoomIds] = useState([]);
  const [key, setKey] = useState(Math.random());
  const selectedRoomDetais = [];
  const { user } = useContext(AuthContext)
  // gia tng hoa n tam thi
  const [totalPriceReservation, setTotalPriceReservation] = useState(0);
  // console.log(dates)
  // var totalRoomQuantitySelected = 0;
  const navigate = useNavigate()

  useEffect(() => {
    dispatch({ type: "NEW_SEARCH", payload: { destination, dates, options } });
  }, [destination, dates, options]);

  // console.log(dates)
  const handleOpen = (i, indexItem) => {
    setSlideNumber(i);       // set vi tri anh trong mang data[index].photos
    setIndexItem(indexItem)  // set vi tri phn t data (tc tng roomType ma ngi dung click vao)
    setOpenExpandPhoto(true); //  m slider anh phong to
  };

  const handleMove = (direction, item) => { // item chinh la data[index], la thng tin room-room cha anh ngi dung c
    let newSlideNumber;

    if (direction === "l") {
      newSlideNumber = slideNumber === 0 ? (item.photos.length - 1) : slideNumber - 1;
    } else {
      newSlideNumber = slideNumber === (item.photos.length - 1) ? 0 : slideNumber + 1;
    }

    setSlideNumber(newSlideNumber);
  };
  // const toggleExpandedPhoto = (index) => {
  //   setExpandedPhotoIndex(index === expandedPhotoIndex ? null : index);
  // };
  // ng dung thay i khoang ngay
  const handleDayChange = (item) => {
    const utc = new Date().getTimezoneOffset() / 60 //-7
    const newSelection = { ...item.selection };
    let { startDate, endDate } = newSelection;
    if (startDate === endDate) {
      // nu ngi dung chi chon 1 ngay
      endDate = addDays(new Date(startDate), 1);
    }
    // 14+ getTimezoneOffset Mui gi lch  khach san ma no t 
    startDate = addHours(startDate, 7 - utc);
    endDate = addHours(endDate, 7 - utc);
    setDates([{ ...newSelection, startDate, endDate }]);
    setSelectedRoomIds([])
    setKey(Math.random()); // Bt reload lai phn chon phong
    setTotalPriceReservation(0)
    reFetch()
  };

  const MILLISECONDS_PER_DAY = 1000 * 60 * 60 * 24;
  function dayDifference(date1, date2) {
    const timeDiff = Math.abs(date2.getTime() - date1.getTime());
    const diffDays = Math.ceil(timeDiff / MILLISECONDS_PER_DAY);
    return diffDays;
  }

  const days = dayDifference(dates[0].endDate, dates[0].startDate);

  const getDatesInRange = (startDate, endDate) => {
    const start = new Date(startDate);
    const end = new Date(endDate);
    const date = new Date(start.getTime());
    const dates = [];
    while (date < end) {
      dates.push(new Date(date).getTime());
      date.setDate(date.getDate() + 1);
    }
    return dates;
  };

  // la chon cua khach
  const alldates = getDatesInRange(dates[0].startDate, dates[0].endDate);
  // alldates.forEach(timestamp => {
  //   const date = new Date(timestamp);
  //   console.log(date.toLocaleDateString());
  // });

  const isAvailable = (roomNumber, dateToCheck) => {
    // if (!roomNumber.status) {
    //   return false; // Nu status l false, room khng kh dng
    // }
    const isFound = roomNumber.unavailableDates.some((date) => {
      const dateMinusOneDay = new Date(date).getTime(); // theem getTIme() hay ko cung v
      // console.log(new Date(dateMinusOneDay));
      return dateToCheck == dateMinusOneDay;
    });

    return !isFound;
  };

  // thay i s lng
  const handleSelectChange = (event, roomNumbers) => {
    let roomQuantitySelected = 0;
    data.forEach((dataItem) => {
      // Ly gi tr c chn t <select> tng ng vi item hin ti
      const selectedValue = parseInt(document.getElementById(`select_${dataItem._id}`).value);
      roomQuantitySelected = roomQuantitySelected + selectedValue;
    });
    // console.log("Tng S lng phng a chon:", roomQuantitySelected);
    const updatedSelectedRooms = selectedRoomIds;

    let updatedSelectedRoomsCopy = [...updatedSelectedRooms];
    // Duyt qua mi phn t trong mng roomNumbers
    roomNumbers.forEach(room => {
      // Kim tra xem _id ca phn t hin ti c tn ti trong mng updatedSelectedRooms khng
      const index = updatedSelectedRoomsCopy.findIndex(selectedRoom => selectedRoom === room._id);
      // Nu c tn ti, loi b phn t  khi mng updatedSelectedRoomsCopy
      if (index !== -1) {
        updatedSelectedRoomsCopy.splice(index, 1);
      }
    });

    roomNumbers.forEach((roomNumber) => {
      if (isAvailable(roomNumber) && updatedSelectedRoomsCopy.length < roomQuantitySelected) {
        updatedSelectedRoomsCopy.push(roomNumber._id);
      }
    });

    setSelectedRoomIds(updatedSelectedRoomsCopy);

    // ghi tng hoa n hin tai
    let totalPrice = 0;
    updatedSelectedRoomsCopy.forEach(roomId => {
      // vi mi _id phong nho thi tim typeRoom tng ng
      const room = data.find(room => room.roomNumbers.some(rn => rn._id == roomId));
      if (room) {
        totalPrice = totalPrice + room.price
      }
    });
    // gia hoa n theo la chon hin tai ng dung
    totalPrice = totalPrice* alldates.length
    setTotalPriceReservation(totalPrice)
  };
  // console.log("Gia hin tai:", totalPriceReservation * alldates.length);

  // ham nut t phong
  const reserveRoom = async () => {
    //check xem ng nhp cha
    if (!user.username) {
      navigate("/login")
      return;
    }

    // Lp qua tng item trong data-la list nhng loai phong, ly ra nhng loai phong ma ngi dung chon
    data.forEach((item) => {
      const selectedValue = parseInt(document.getElementById(`select_${item._id}`).value);
      // Kim tra nu s lng phng  chn khc 0
      if (selectedValue !== 0) {
        // Thm i tng vo mng selectedRoomDetais, mang nay  s dung cho bn reserve
        selectedRoomDetais.push({ roomTypeId: item._id, typeRoom: item.title, quantity: selectedValue });
      }

    });
    // seletedRoomIdsReserved gm roomtype id, roomtype name, quantity, con selectedRoomIds gm id nhng phong nho
    if (selectedRoomIds.length > 0 && alldates.length >= 1) {
      navigate("/reserve", {
        state: {
          selectedRoomIds, alldates, hotelId, startDate: dates[0].startDate, endDate: dates[0].endDate,
          seletedRoomIdsReserved: selectedRoomDetais
        }
      });
      //seletedRoomIdsReserved la mang  truyn vao reserve x ly tim id sau
    } else {
      toast.error('Ban cha chon phong mun t hoc ngay ban chon phai ti thiu 1 m');
    }

  };



  return (
    <div className="RoomClientContainer">
      {/* tieu de va thanh search ngay */}
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <h1>Ban mun t phong?</h1>
        {/* Hin thi ngay */}
        <div style={{ width: '30%' }} className="RoomClientContainer_searchDates">
          <FontAwesomeIcon icon={faCalendarDays} className="RoomClientContainer_icon" />
          <span onClick={() => setOpenDate(!openDate)}>{`${format(
            dates[0].startDate,
            "dd/MM/yyyy"
          )} n ${format(dates[0].endDate, "dd/MM/yyyy")}`}</span>
          {openDate && (
            <DateRange
              onChange={(item) => handleDayChange(item)}
              minDate={addHours(new Date(),10+(-7-timeZone))} // ngay ti thiu c chon, 
              maxDate={addDays(new Date(),365)}
              ranges={dates}
              moveRangeOnFirstSelection={true}
              editableDateInputs={true}
              className="date"
            />

          )}
        </div>

        {/* Hin s ngi */}
        <div ><FontAwesomeIcon icon={faPerson} className="RoomClientContainer_icon" /> {options.adult} ngi ln, {options.children} tre em</div>

      </div>



      {/* Phn t phong gom nhieu flex_div */}
      {data.map((item, index) => (
        <div className="flex_div" style={{ border: '1px solid #7fc7af' }}>

          <div style={{ width: '50%' }} >
            <div className="rTitle">{item.title}</div>
            <div className="rDesc" >S lng ngi: {item.maxPeople}</div>
            <div className="rMax">{item.desc}</div>
            <div className="rImages">
              {item.photos?.slice(0, 3).map((photo, i) => (
                <div className="rImgWrapper" key={i}>
                  <img
                    src={photo}
                    alt=""
                    className="roomImg"
                    onClick={() => handleOpen(i, index)}
                  />
                  {i === 2 && item.photos.length > 3 && (
                    <div style={{ fontStyle: 'italic' }}>
                      (Con {item.photos.length - 3} anh tip...)
                    </div>
                  )}
                  {/* Kim tra nu index ca nh c click trng vi expandedPhotoIndex th hin th nh phng to */}
                  {openExpandPhoto && (
                    <div className="expandedPhotoWrapper">
                      {/* css dung t hotel */}
                      <FontAwesomeIcon
                        icon={faCircleXmark}
                        className="close"
                        onClick={() => setOpenExpandPhoto(false)}
                      />
                      <FontAwesomeIcon
                        icon={faCircleArrowLeft}
                        className="arrow"
                        onClick={() => handleMove("l", data[indexItem])}
                      />
                      {/* hin thi anh phong to */}
                      <div className="sliderWrapper">
                        <img
                          src={data[indexItem].photos[slideNumber]}
                          alt=""
                          className="sliderImg"
                        />
                      </div>
                      <FontAwesomeIcon
                        icon={faCircleArrowRight}
                        className="arrow"
                        onClick={() => handleMove("r", data[indexItem])}
                      />

                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>

          {/*  hin gia */}
          <div>
            Gia: {new Intl.NumberFormat('vi-VN').format(item.price * 1000)} VND
            <br />
            (Mi m)
          </div>
          {/* cha ch chon phong */}
          <div key={key} style={{ marginBottom: '10px', display: 'flex', justifyContent: 'flex-end', width: '20%', alignItems: 'center', gap: '10px' }}>

            <select style={{ height: '20px' }} id={`select_${item._id}`} onChange={(event) => handleSelectChange(event, item.roomNumbers)}>

              {/* <option id={`defaultOption_${item._id}`} value={0}>0 phng</option> */}
              {(() => {
                let roomAvailable = 999;
                for (let date of alldates) {
                  let dateAvailableCount = 0;
                  //Vi mi date, duyt qua cac phn t trong mng roomNumbers
                  for (let roomNumber of item.roomNumbers) {
                    // Kim tra xem phng o co date hin tai trng ko
                    if (isAvailable(roomNumber, date)) {
                      // co phong thoa man date hin tai
                      dateAvailableCount++
                    }
                  };
                  // vi mi date sau khi lp ht cac room nho, cp nht roomAvailable (roomAvailable se la 
                  // dateAvailableCount nho nht trong tt ca cac date )
                  if (dateAvailableCount < roomAvailable) {
                    roomAvailable = dateAvailableCount
                  }

                }
                const maxOptions = 10; // S lng phong ti a se hin ca the option
                const options = [];
                //  ko con phong trng nao
                if (roomAvailable === 0) {
                  options.push(<option value={0}>a ht</option>);
                } else {
                  options.push(<option value={0}> 0 {hotelType === "Khach san"||hotelType === "Nha nghi" ? "phong" : hotelType.toLowerCase()}</option>);
                  for (let i = 1; i <= roomAvailable; i++) {
                    if (i > maxOptions) break;
                    options.push(<option value={i}>{i} {hotelType === "Khach san" ||hotelType === "Nha nghi" ? "phong" : hotelType.toLowerCase()}</option>);
                  }
                }


                return options;


                // let roomIndex = 0; 
                // const maxOptions = 10; // S lng phong ti a se hin ca the option
                // let optionsCount = 0; // Bin m s lng option  c thm vo
                // let hasAvailableRoom = false; // Bin kim tra xem c phng no tha mn khng
                // const options= item.roomNumbers.map((roomNumber, index) => {
                //   if (optionsCount >= maxOptions) return null; // Nu  thm  s lng ti a option th dng
                //   if (isAvailable(roomNumber)) {
                //     roomIndex++; // Tng gi tr bin m khi phng tha mn iu kin
                //     optionsCount++; 
                //     hasAvailableRoom = true;
                //     return (
                //       <option key={roomNumber._id} value={roomIndex}>
                //         {`${roomIndex} phng`}
                //       </option>
                //     );
                //   }
                //   return null;
                // });

                // if(hasAvailableRoom){
                //   return (
                //     <>
                //       <option  value={0}>0 phng</option>
                //       {options}
                //     </>
                //   );
                // } else{
                //   // ko con phong nao
                //   return (
                //     <option  value={0}>Ht phng</option>
                //     );
                // } 

              })
                ()}
            </select>

            <div style={{ width: '30%', height: '100%', fontSize: '14px' }}>(Chon s lng mun t)</div>

          </div>



        </div>
      ))}

      <h2>Ban a chon {selectedRoomIds.length} phong</h2>
      {totalPriceReservation > 0 && (
        <h2>Tng gia: {new Intl.NumberFormat('vi-VN').format(totalPriceReservation * 1000)} VND</h2>
      )}

      <button onClick={reserveRoom} className="rButton">
        i n trang t phong
      </button>


    </div>
  )
}

export default ListRoomClient

